import React from 'react';
import {
  UserGroupIcon,
  AcademicCapIcon,
  BriefcaseIcon,
  PlusCircleIcon,
} from '@heroicons/react/24/outline';
import { DemographicConfig } from './DemographicSurveyConfig';

export interface SurveyTemplate {
  id: string;
  name: string;
  description: string;
  icon: React.ComponentType<any>;
  config: Partial<DemographicConfig>;
  recommendedFor: string[];
}

// 3   
export const SURVEY_TEMPLATES: SurveyTemplate[] = [
  {
    id: 'basic',
    name: ' ',
    description: '      .',
    icon: UserGroupIcon,
    recommendedFor: [' ', ' ', ' '],
    config: {
      enabled: true,
      useAge: true,
      useGender: true,
      useEducation: true,
      useOccupation: false,
      useIndustry: false,
      useExperience: false,
      surveyTitle: '  ',
      surveyDescription: '    .',
      estimatedTime: 2,
      customQuestions: [],
    },
  },
  {
    id: 'professional',
    name: ' ',
    description: '        .',
    icon: BriefcaseIcon,
    recommendedFor: [' ', ' ', 'B2B '],
    config: {
      enabled: true,
      useAge: true,
      useGender: true,
      useEducation: true,
      useOccupation: true,
      useIndustry: true,
      useExperience: true,
      surveyTitle: '  ',
      surveyDescription: '    .',
      estimatedTime: 3,
      customQuestions: [
        {
          id: 'expertise_area',
          type: 'text',
          question: '   ',
          required: true,
        },
        {
          id: 'certifications',
          type: 'text',
          question: '   ',
          required: false,
        },
      ],
    },
  },
  {
    id: 'academic',
    name: '  ',
    description: '         .',
    icon: AcademicCapIcon,
    recommendedFor: [' ', ' ', ' '],
    config: {
      enabled: true,
      useAge: true,
      useGender: true,
      useEducation: true,
      useOccupation: true,
      useIndustry: true,
      useExperience: true,
      surveyTitle: '   ',
      surveyDescription: '      .',
      estimatedTime: 4,
      customQuestions: [
        {
          id: 'research_experience',
          type: 'select',
          question: '   ',
          options: ['', '1-2', '3-5', '6 '],
          required: true,
        },
        {
          id: 'publication_count',
          type: 'select',
          question: '   ',
          options: ['', '1-3', '4-10', '10 '],
          required: false,
        },
        {
          id: 'research_field',
          type: 'text',
          question: '  ',
          required: true,
        },
      ],
    },
  },
];

interface DemographicSurveyTemplatesProps {
  onSelectTemplate: (template: SurveyTemplate | null) => void;
  selectedTemplateId?: string | null;
}

const DemographicSurveyTemplates: React.FC<DemographicSurveyTemplatesProps> = ({
  onSelectTemplate,
  selectedTemplateId,
}) => {
  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-2">
            
        </h3>
        <p className="text-sm text-gray-600 mb-4">
                
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/*    */}
        {SURVEY_TEMPLATES.map((template) => {
          const Icon = template.icon;
          const isSelected = selectedTemplateId === template.id;

          return (
            <div
              key={template.id}
              onClick={() => onSelectTemplate(template)}
              className={`
                relative p-6 rounded-xl border-2 cursor-pointer transition-all
                ${
                  isSelected
                    ? 'border-primary bg-primary/5 shadow-lg'
                    : 'border-gray-200 hover:border-primary/50 hover:shadow-md'
                }
              `}
            >
              {/*   */}
              {isSelected && (
                <div className="absolute top-3 right-3">
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center">
                    <svg
                      className="w-4 h-4 text-white"
                      fill="currentColor"
                      viewBox="0 0 20 20"
                    >
                      <path
                        fillRule="evenodd"
                        d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                        clipRule="evenodd"
                      />
                    </svg>
                  </div>
                </div>
              )}

              <div className="flex items-start gap-4">
                <div className="flex-shrink-0">
                  <div
                    className={`
                      w-12 h-12 rounded-lg flex items-center justify-center
                      ${
                        isSelected
                          ? 'bg-primary text-white'
                          : 'bg-gray-100 text-gray-600'
                      }
                    `}
                  >
                    <Icon className="h-6 w-6" />
                  </div>
                </div>

                <div className="flex-1">
                  <h4 className="font-semibold text-gray-900 mb-1">
                    {template.name}
                  </h4>
                  <p className="text-sm text-gray-600 mb-3">
                    {template.description}
                  </p>

                  {/*    */}
                  <div className="flex flex-wrap gap-1">
                    {template.recommendedFor.map((use, index) => (
                      <span
                        key={index}
                        className="inline-block px-2 py-1 text-xs bg-gray-100 text-gray-600 rounded-full"
                      >
                        {use}
                      </span>
                    ))}
                  </div>

                  {/*    */}
                  <div className="mt-3 pt-3 border-t border-gray-100">
                    <p className="text-xs text-gray-500">
                       :{' '}
                      {[
                        template.config.useAge && '',
                        template.config.useGender && '',
                        template.config.useEducation && '',
                        template.config.useOccupation && '',
                        template.config.useIndustry && '',
                        template.config.useExperience && '',
                      ]
                        .filter(Boolean)
                        .join(', ')}
                      {template.config.customQuestions &&
                        template.config.customQuestions.length > 0 &&
                        ` +${template.config.customQuestions.length}  `}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          );
        })}

        {/*    */}
        <div
          onClick={() => onSelectTemplate(null)}
          className={`
            relative p-6 rounded-xl border-2 cursor-pointer transition-all
            ${
              selectedTemplateId === 'custom'
                ? 'border-primary bg-primary/5 shadow-lg'
                : 'border-gray-200 hover:border-primary/50 hover:shadow-md border-dashed'
            }
          `}
        >
          {selectedTemplateId === 'custom' && (
            <div className="absolute top-3 right-3">
              <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center">
                <svg
                  className="w-4 h-4 text-white"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                >
                  <path
                    fillRule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clipRule="evenodd"
                  />
                </svg>
              </div>
            </div>
          )}

          <div className="flex items-start gap-4">
            <div className="flex-shrink-0">
              <div
                className={`
                  w-12 h-12 rounded-lg flex items-center justify-center
                  ${
                    selectedTemplateId === 'custom'
                      ? 'bg-primary text-white'
                      : 'bg-gray-100 text-gray-600'
                  }
                `}
              >
                <PlusCircleIcon className="h-6 w-6" />
              </div>
            </div>

            <div className="flex-1">
              <h4 className="font-semibold text-gray-900 mb-1">
                 
              </h4>
              <p className="text-sm text-gray-600 mb-3">
                       
              </p>

              <div className="flex flex-wrap gap-1">
                <span className="inline-block px-2 py-1 text-xs bg-primary/10 text-primary rounded-full">
                   
                </span>
                <span className="inline-block px-2 py-1 text-xs bg-primary/10 text-primary rounded-full">
                   
                </span>
              </div>

              <div className="mt-3 pt-3 border-t border-gray-100">
                <p className="text-xs text-gray-500">
                       
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*     */}
      {selectedTemplateId && selectedTemplateId !== 'custom' && (
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <p className="text-sm text-blue-800">
                      .
          </p>
        </div>
      )}
    </div>
  );
};

export default DemographicSurveyTemplates;