import React, { useState, useEffect } from 'react';
import UIIcon from '../common/UIIcon';
import PageHeader from '../common/PageHeader';
import { PrimaryButton, SecondaryButton, SuccessButton, DangerButton } from '../common/UIButton';
import { useColorTheme, ColorTheme } from '../../hooks/useColorTheme';
import api from '../../services/api';
import type { User } from '../../types';

interface PersonalSettingsProps {
  user: User;
  onBack?: () => void;
  onUserUpdate?: (updatedUser: User) => void;
}

interface UserSettings {
  profile: {
    firstName: string;
    lastName: string;
    email: string;
    organization: string;
    department: string;
    phone: string;
    profileImage: string;
  };
  security: {
    twoFactorEnabled: boolean;
    sessionTimeout: number;
    loginAlerts: boolean;
  };
  workflow: {
    autoSaveInterval: number;
    defaultTemplate: string;
    screenLayout: string;
    defaultViewMode: string;
    showTutorials: boolean;
  };
  notifications: {
    emailNotifications: boolean;
    evaluationComplete: boolean;
    projectStatusChange: boolean;
    weeklyReport: boolean;
    systemUpdates: boolean;
    deadlineReminders: boolean;
  };
  display: {
    theme: ColorTheme | 'auto';
    darkMode: boolean;
    language: string;
    dateFormat: string;
    numberFormat: string;
    timezone: string;
  };
  privacy: {
    profileVisibility: 'public' | 'private' | 'team';
    showEmail: boolean;
    showPhone: boolean;
    activityTracking: boolean;
  };
}

const PersonalSettings: React.FC<PersonalSettingsProps> = ({ user, onBack, onUserUpdate }) => {
  const { currentTheme, changeColorTheme } = useColorTheme();
  const [activeTab, setActiveTab] = useState<'profile' | 'security' | 'workflow' | 'notifications' | 'display' | 'privacy' | 'data'>('profile');

  //      (localStorage  )
  const getInitialSettings = (): UserSettings => {
    return {
      profile: {
        firstName: user.first_name,
        lastName: user.last_name,
        email: user.email,
        organization: '',
        department: '',
        phone: '',
        profileImage: ''
      },
      security: {
        twoFactorEnabled: false,
        sessionTimeout: 30,
        loginAlerts: true
      },
      workflow: {
        autoSaveInterval: 60,
        defaultTemplate: 'standard',
        screenLayout: 'standard',
        defaultViewMode: 'grid',
        showTutorials: true
      },
      notifications: {
        emailNotifications: true,
        evaluationComplete: true,
        projectStatusChange: true,
        weeklyReport: false,
        systemUpdates: false,
        deadlineReminders: true
      },
      display: {
        theme: currentTheme,
        darkMode: false,
        language: 'ko',
        dateFormat: 'YYYY-MM-DD',
        numberFormat: '1,234.56',
        timezone: 'Asia/Seoul'
      },
      privacy: {
        profileVisibility: 'team',
        showEmail: false,
        showPhone: false,
        activityTracking: true
      }
    };
  };

  const [settings, setSettings] = useState<UserSettings>(getInitialSettings());

  const [passwordForm, setPasswordForm] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });

  const [saveStatus, setSaveStatus] = useState<'idle' | 'saving' | 'saved' | 'error'>('idle');
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [showImportModal, setShowImportModal] = useState(false);
  const [pendingImportData, setPendingImportData] = useState<UserSettings | null>(null);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  //     (localStorage )
  useEffect(() => {
    const loadUserDataFromAPI = async () => {
      const response = await api.get('/api/service/auth/profile/');

      if (response.success && response.data) {
        const data = response.data;
        if (data.user) {
          const apiSettings = {
            profile: {
              firstName: data.user.first_name || settings.profile.firstName,
              lastName: data.user.last_name || settings.profile.lastName,
              email: data.user.email || settings.profile.email,
              phone: data.user.phone || settings.profile.phone,
              organization: data.user.organization || settings.profile.organization,
              department: data.user.department || settings.profile.department,
              profileImage: data.user.profile_image || settings.profile.profileImage
            },
            security: settings.security,
            workflow: settings.workflow,
            notifications: data.user.notifications || settings.notifications,
            display: {
              ...settings.display,
              theme: data.user.theme || settings.display.theme,
              language: data.user.language || settings.display.language
            },
            privacy: settings.privacy
          };

          setSettings(apiSettings);

          //     
          if (onUserUpdate && (data.user.first_name !== user.first_name || data.user.last_name !== user.last_name)) {
            onUserUpdate({
              ...user,
              first_name: data.user.first_name,
              last_name: data.user.last_name
            });
          }
        }
      } else if (response.error?.includes('  ')) {
        // DB     
        saveSettingsToAPI();
      }
    };

    loadUserDataFromAPI();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  //  API  (localStorage  )
  const saveSettingsToAPI = async () => {
    setSaveStatus('saving');

    try {
      //    ()
      if (settings.display.theme !== 'auto' && settings.display.theme !== currentTheme) {
        changeColorTheme(settings.display.theme);
      }

      //      UI 
      const isNameChanged = settings.profile.firstName !== user.first_name || settings.profile.lastName !== user.last_name;

      if (isNameChanged && onUserUpdate) {
        //     
        const updatedUser = {
          ...user,
          first_name: settings.profile.firstName,
          last_name: settings.profile.lastName,
          _updated: Date.now()
        };
        onUserUpdate(updatedUser);
      }

      // API  
      const requestData = {
        first_name: settings.profile.firstName,
        last_name: settings.profile.lastName,
        email: settings.profile.email,
        phone: settings.profile.phone,
        organization: settings.profile.organization,
        department: settings.profile.department,
        notifications: settings.notifications,
        theme: settings.display.theme,
        language: settings.display.language
      };

      const response = await api.put('/api/service/auth/profile/', requestData);

      if (response.success) {
        setSaveStatus('saved');
        setTimeout(() => setSaveStatus('idle'), 2000);
      } else {
        throw new Error(response.error || 'API  ');
      }
    } catch (error) {
      setSaveStatus('error');
      setTimeout(() => setSaveStatus('idle'), 3000);
    }
  };

  //   
  const handleDataImport = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    try {
      const text = await file.text();
      const importData = JSON.parse(text);
      setPendingImportData(importData.settings || importData);
      setShowImportModal(true);
    } catch (error) {
      showActionMessage('error', '    .');
    }
  };

  const confirmDataImport = async () => {
    setShowImportModal(false);
    if (pendingImportData) {
      setSettings(pendingImportData);
      await saveSettingsToAPI();
      showActionMessage('success', '  .');
      setPendingImportData(null);
    }
  };

  //   
  const handleDataExport = () => {
    const exportData = {
      version: '1.0',
      exportDate: new Date().toISOString(),
      user: {
        id: user.id,
        email: user.email,
        role: user.role
      },
      settings
    };

    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `ahp-settings-${user.email}-${new Date().toISOString().split('T')[0]}.json`;
    a.click();
    URL.revokeObjectURL(url);
  };

  //   
  const handlePasswordChange = async () => {
    if (passwordForm.newPassword !== passwordForm.confirmPassword) {
      showActionMessage('error', '   .');
      return;
    }

    const response = await api.put('/api/service/auth/change-password/', {
      current_password: passwordForm.currentPassword,
      new_password: passwordForm.newPassword,
    });

    if (response.success) {
      showActionMessage('success', '  .');
      setPasswordForm({ currentPassword: '', newPassword: '', confirmPassword: '' });
    } else {
      showActionMessage('error', response.error || '  .');
    }
  };

  //   
  const handleDeleteAccount = () => {
    setShowDeleteModal(true);
  };

  const confirmDeleteAccount = async () => {
    setShowDeleteModal(false);

    const response = await api.delete('/api/service/auth/delete-account/');

    if (response.success) {
      showActionMessage('success', ' .');
      //  
      window.location.href = '/';
    } else {
      showActionMessage('error', response.error || '  .');
    }
  };

  const updateSettings = (section: keyof UserSettings, field: string, value: unknown) => {
    setSettings(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: value
      }
    }));
  };

  const getSaveStatusText = () => {
    switch (saveStatus) {
      case 'saving': return ' ...';
      case 'saved': return '';
      case 'error': return ' ';
      default: return '';
    }
  };


  const tabs = [
    { id: 'profile', label: '', icon: '' },
    { id: 'security', label: '', icon: '' },
    { id: 'workflow', label: '', icon: '' },
    { id: 'notifications', label: '', icon: '' },
    { id: 'display', label: '', icon: '' },
    { id: 'privacy', label: '', icon: '' },
    { id: 'data', label: ' ', icon: '' }
  ];

  return (
    <div className="min-h-screen" style={{ backgroundColor: 'var(--bg-base)' }}>
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}

      {/*     */}
      {showDeleteModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black bg-opacity-50" onClick={() => setShowDeleteModal(false)} />
          <div className="relative bg-white rounded-xl shadow-2xl p-6 max-w-sm w-full mx-4">
            <h3 className="text-lg font-bold text-gray-900 mb-2">  </h3>
            <p className="text-gray-600 text-sm mb-6">
                ?         .
            </p>
            <div className="flex space-x-3">
              <button
                onClick={() => setShowDeleteModal(false)}
                className="flex-1 px-4 py-2 rounded-lg border border-gray-300 text-gray-700 text-sm font-medium hover:bg-gray-50 transition-colors"
              >
                
              </button>
              <button
                onClick={confirmDeleteAccount}
                className="flex-1 px-4 py-2 rounded-lg bg-red-600 text-white text-sm font-medium hover:bg-red-700 transition-colors"
              >
                 
              </button>
            </div>
          </div>
        </div>
      )}

      {/*     */}
      {showImportModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black bg-opacity-50" onClick={() => setShowImportModal(false)} />
          <div className="relative bg-white rounded-xl shadow-2xl p-6 max-w-sm w-full mx-4">
            <h3 className="text-lg font-bold text-gray-900 mb-2">  </h3>
            <p className="text-gray-600 text-sm mb-6">
                  ?
            </p>
            <div className="flex space-x-3">
              <button
                onClick={() => { setShowImportModal(false); setPendingImportData(null); }}
                className="flex-1 px-4 py-2 rounded-lg border border-gray-300 text-gray-700 text-sm font-medium hover:bg-gray-50 transition-colors"
              >
                
              </button>
              <button
                onClick={confirmDataImport}
                className="flex-1 px-4 py-2 rounded-lg bg-blue-600 text-white text-sm font-medium hover:bg-blue-700 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      )}

      <PageHeader
        title=" "
        description="    "
        icon=""
        onBack={onBack || (() => window.history.back())}
        actions={
          <SuccessButton
            onClick={saveSettingsToAPI}
            disabled={saveStatus === 'saving'}
            className="flex items-center space-x-2"
          >
            <UIIcon emoji="" size="lg" color="white" />
            <span>{getSaveStatusText()}</span>
          </SuccessButton>
        }
      />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6 space-y-6">

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/*   */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm border border-gray-200">
              <nav className="p-4 space-y-2">
                {tabs.map(tab => (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id as 'profile' | 'security' | 'workflow' | 'notifications' | 'display' | 'privacy' | 'data')}
                    className={`w-full text-left px-4 py-3 rounded-lg transition-all duration-200 flex items-center space-x-3 ${
                      activeTab === tab.id
                        ? 'bg-blue-600 text-white shadow-md'
                        : 'text-gray-700 hover:bg-gray-50 border border-gray-200'
                    }`}
                  >
                    <UIIcon emoji={tab.icon} size="lg" color={activeTab === tab.id ? 'white' : 'primary'} />
                    <span className="font-medium">{tab.label}</span>
                  </button>
                ))}
              </nav>
            </div>
          </div>

          {/*   */}
          <div className="lg:col-span-3">
            {activeTab === 'profile' && (
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
                <div className="flex items-center space-x-3 mb-6">
                  <UIIcon emoji="" size="xl" color="primary" />
                  <h2 className="text-2xl font-bold text-gray-900"> </h2>
                </div>

                <div className="space-y-6">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="text"
                        value={settings.profile.firstName}
                        onChange={(e) => updateSettings('profile', 'firstName', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="text"
                        value={settings.profile.lastName}
                        onChange={(e) => updateSettings('profile', 'lastName', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="email"
                        value={settings.profile.email}
                        onChange={(e) => updateSettings('profile', 'email', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="tel"
                        value={settings.profile.phone}
                        onChange={(e) => updateSettings('profile', 'phone', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="text"
                        value={settings.profile.organization}
                        onChange={(e) => updateSettings('profile', 'organization', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}></label>
                      <input
                        type="text"
                        value={settings.profile.department}
                        onChange={(e) => updateSettings('profile', 'department', e.target.value)}
                        className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                        style={{
                          backgroundColor: 'var(--bg-primary)',
                          borderColor: 'var(--border-light)',
                          color: 'var(--text-primary)'
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'security' && (
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
                <div className="flex items-center space-x-3 mb-6">
                  <UIIcon emoji="" size="xl" color="primary" />
                  <h2 className="text-2xl font-bold text-gray-900"> </h2>
                </div>

                <div className="space-y-6">
                  <div>
                    <h3 className="text-lg font-medium mb-4 flex items-center space-x-2" style={{ color: 'var(--text-primary)' }}>
                      <UIIcon emoji="" size="lg" color="primary" />
                      <span> </span>
                    </h3>
                    <div className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}> </label>
                        <input
                          type="password"
                          value={passwordForm.currentPassword}
                          onChange={(e) => setPasswordForm({...passwordForm, currentPassword: e.target.value})}
                          className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                          style={{
                            backgroundColor: 'var(--bg-primary)',
                            borderColor: 'var(--border-light)',
                            color: 'var(--text-primary)'
                          }}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}> </label>
                        <input
                          type="password"
                          value={passwordForm.newPassword}
                          onChange={(e) => setPasswordForm({...passwordForm, newPassword: e.target.value})}
                          className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                          style={{
                            backgroundColor: 'var(--bg-primary)',
                            borderColor: 'var(--border-light)',
                            color: 'var(--text-primary)'
                          }}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}>  </label>
                        <input
                          type="password"
                          value={passwordForm.confirmPassword}
                          onChange={(e) => setPasswordForm({...passwordForm, confirmPassword: e.target.value})}
                          className="w-full px-4 py-3 rounded-lg border transition-colors focus:outline-none focus:ring-2"
                          style={{
                            backgroundColor: 'var(--bg-primary)',
                            borderColor: 'var(--border-light)',
                            color: 'var(--text-primary)'
                          }}
                        />
                      </div>
                      <PrimaryButton onClick={handlePasswordChange} className="flex items-center space-x-2">
                        <UIIcon emoji="" size="lg" color="white" />
                        <span> </span>
                      </PrimaryButton>
                    </div>
                </div>

                <div>
                  <h3 className="text-lg font-medium mb-4"> </h3>
                  <div className="space-y-4">
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        checked={settings.security.twoFactorEnabled}
                        onChange={(e) => updateSettings('security', 'twoFactorEnabled', e.target.checked)}
                        className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                      />
                      <span className="ml-2">2  </span>
                    </label>
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        checked={settings.security.loginAlerts}
                        onChange={(e) => updateSettings('security', 'loginAlerts', e.target.checked)}
                        className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                      />
                      <span className="ml-2">  </span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          )}

          {activeTab === 'workflow' && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <div className="flex items-center space-x-3 mb-6">
                <UIIcon emoji="" size="xl" color="primary" />
                <h2 className="text-2xl font-bold text-gray-900"> </h2>
              </div>
              <div className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">   ()</label>
                  <select
                    value={settings.workflow.autoSaveInterval}
                    onChange={(e) => updateSettings('workflow', 'autoSaveInterval', parseInt(e.target.value))}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value={30}>30</option>
                    <option value={60}>1</option>
                    <option value={120}>2</option>
                    <option value={300}>5</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <select
                    value={settings.workflow.defaultTemplate}
                    onChange={(e) => updateSettings('workflow', 'defaultTemplate', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="standard"></option>
                    <option value="academic"></option>
                    <option value="business"></option>
                  </select>
                </div>

                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={settings.workflow.showTutorials}
                    onChange={(e) => updateSettings('workflow', 'showTutorials', e.target.checked)}
                    className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                  />
                  <span className="ml-2"> </span>
                </label>
              </div>
            </div>
          )}

          {activeTab === 'notifications' && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <div className="flex items-center space-x-3 mb-6">
                <UIIcon emoji="" size="xl" color="primary" />
                <h2 className="text-2xl font-bold text-gray-900"> </h2>
              </div>
              <div className="space-y-4">
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={settings.notifications.emailNotifications}
                    onChange={(e) => updateSettings('notifications', 'emailNotifications', e.target.checked)}
                    className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                  />
                  <span className="ml-2">  </span>
                </label>
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={settings.notifications.evaluationComplete}
                    onChange={(e) => updateSettings('notifications', 'evaluationComplete', e.target.checked)}
                    className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                  />
                  <span className="ml-2">  </span>
                </label>
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={settings.notifications.projectStatusChange}
                    onChange={(e) => updateSettings('notifications', 'projectStatusChange', e.target.checked)}
                    className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                  />
                  <span className="ml-2">   </span>
                </label>
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    checked={settings.notifications.deadlineReminders}
                    onChange={(e) => updateSettings('notifications', 'deadlineReminders', e.target.checked)}
                    className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                  />
                  <span className="ml-2"> </span>
                </label>
              </div>
            </div>
          )}

          {activeTab === 'display' && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <div className="flex items-center space-x-3 mb-6">
                <UIIcon emoji="" size="xl" color="primary" />
                <h2 className="text-2xl font-bold text-gray-900"> </h2>
              </div>
              <div className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                  <select
                    value={settings.display.language}
                    onChange={(e) => updateSettings('display', 'language', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="ko"></option>
                    <option value="en">English</option>
                    <option value="ja"></option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <select
                    value={settings.display.dateFormat}
                    onChange={(e) => updateSettings('display', 'dateFormat', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="YYYY-MM-DD">2024-01-01</option>
                    <option value="MM/DD/YYYY">01/01/2024</option>
                    <option value="DD/MM/YYYY">01/01/2024</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                  <select
                    value={settings.display.timezone}
                    onChange={(e) => updateSettings('display', 'timezone', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="Asia/Seoul">  (KST)</option>
                    <option value="UTC">  (UTC)</option>
                    <option value="America/New_York">  (EST)</option>
                  </select>
                </div>
              </div>
            </div>
          )}

          {activeTab === 'privacy' && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <div className="flex items-center space-x-3 mb-6">
                <UIIcon emoji="" size="xl" color="primary" />
                <h2 className="text-2xl font-bold text-gray-900"> </h2>
              </div>
              <div className="space-y-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">  </label>
                  <select
                    value={settings.privacy.profileVisibility}
                    onChange={(e) => updateSettings('privacy', 'profileVisibility', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="public"></option>
                    <option value="team">  </option>
                    <option value="private"></option>
                  </select>
                </div>

                <div className="space-y-4">
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={settings.privacy.showEmail}
                      onChange={(e) => updateSettings('privacy', 'showEmail', e.target.checked)}
                      className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                    />
                    <span className="ml-2">  </span>
                  </label>
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={settings.privacy.showPhone}
                      onChange={(e) => updateSettings('privacy', 'showPhone', e.target.checked)}
                      className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                    />
                    <span className="ml-2"> </span>
                  </label>
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={settings.privacy.activityTracking}
                      onChange={(e) => updateSettings('privacy', 'activityTracking', e.target.checked)}
                      className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                    />
                    <span className="ml-2">  </span>
                  </label>
                </div>
              </div>
            </div>
          )}

            {activeTab === 'data' && (
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
                <div className="flex items-center space-x-3 mb-6">
                  <UIIcon emoji="" size="xl" color="primary" />
                  <h2 className="text-2xl font-bold text-gray-900"> </h2>
                </div>

                <div className="space-y-6">
                  <div>
                    <h3 className="text-lg font-medium mb-4 flex items-center space-x-2" style={{ color: 'var(--text-primary)' }}>
                      <UIIcon emoji="" size="lg" color="primary" />
                      <span>   </span>
                    </h3>
                    <div className="flex space-x-4">
                      <PrimaryButton onClick={handleDataExport} className="flex items-center space-x-2">
                        <UIIcon emoji="" size="lg" color="white" />
                        <span> </span>
                      </PrimaryButton>
                      <label className="cursor-pointer">
                        <SecondaryButton className="flex items-center space-x-2">
                          <UIIcon emoji="" size="lg" color="primary" />
                          <span> </span>
                        </SecondaryButton>
                        <input
                          type="file"
                          accept=".json"
                          onChange={handleDataImport}
                          className="hidden"
                        />
                      </label>
                    </div>
                  </div>

                  <div className="border-t pt-6" style={{ borderColor: 'var(--border-light)' }}>
                    <h3 className="text-lg font-medium mb-4 flex items-center space-x-2" style={{ color: 'var(--error-primary)' }}>
                      <UIIcon emoji="" size="lg" color="error" />
                      <span> </span>
                    </h3>
                    <div className="p-4 rounded-lg border" style={{
                      backgroundColor: 'var(--error-pastel)',
                      borderColor: 'var(--error-light)'
                    }}>
                      <p className="mb-4" style={{ color: 'var(--error-dark)' }}>
                             .
                      </p>
                      <DangerButton onClick={handleDeleteAccount} className="flex items-center space-x-2">
                        <UIIcon emoji="" size="lg" color="white" />
                        <span> </span>
                      </DangerButton>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default PersonalSettings;
