import React, { useState } from 'react';
import Card from '../common/Card';

interface PaymentPlan {
  id: string;
  name: string;
  price: number;
  originalPrice?: number;
  period: string;
  features: {
    projects: number;
    users: number;
    evaluators: number;
    support: string;
    export: boolean;
    api: boolean;
  };
  popular?: boolean;
}

const paymentPlans: PaymentPlan[] = [
  {
    id: 'free',
    name: ' ',
    price: 0,
    period: '7',
    features: {
      projects: 1,
      users: 1,
      evaluators: 3,
      support: '',
      export: false,
      api: false
    }
  },
  {
    id: 'starter',
    name: 'Starter',
    price: 29000,
    period: '',
    features: {
      projects: 5,
      users: 1,
      evaluators: 10,
      support: '',
      export: true,
      api: false
    }
  },
  {
    id: 'professional',
    name: 'Professional',
    price: 59000,
    originalPrice: 79000,
    period: '',
    features: {
      projects: 20,
      users: 5,
      evaluators: 50,
      support: ' ',
      export: true,
      api: true
    },
    popular: true
  },
  {
    id: 'enterprise',
    name: 'Enterprise',
    price: 199000,
    period: '',
    features: {
      projects: -1, // unlimited
      users: -1,
      evaluators: -1,
      support: ' ',
      export: true,
      api: true
    }
  }
];

const PaymentSystem: React.FC = () => {
  const [selectedPlan, setSelectedPlan] = useState<string>('professional');
  const [billingCycle, setBillingCycle] = useState<'monthly' | 'yearly'>('monthly');
  const [paymentMethod, setPaymentMethod] = useState<string>('card');

  const getDiscountedPrice = (price: number): number => {
    if (billingCycle === 'yearly') {
      return Math.floor(price * 10); // 2 
    }
    return price;
  };

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">

      {/*    */}
      <div className="mb-8 rounded-xl border-2 border-amber-300 bg-amber-50 p-6 text-center shadow-sm">
        <div className="text-4xl mb-3"></div>
        <h2 className="text-xl font-bold text-amber-900 mb-2">    </h2>
        <p className="text-amber-800 mb-1">
          <strong>KG</strong>      .
            <strong></strong>     .
        </p>
        <p className="text-sm text-amber-700 mb-4">
               .
        </p>
        <div className="inline-flex flex-col sm:flex-row gap-3 justify-center">
          <a
            href="mailto:contact@ahp-platform.com"
            className="inline-flex items-center justify-center gap-2 px-5 py-2.5 rounded-lg bg-amber-600 text-white font-medium hover:bg-amber-700 transition-colors"
          >
              
          </a>
          <span className="inline-flex items-center justify-center gap-2 px-5 py-2.5 rounded-lg bg-white border border-amber-300 text-amber-800 text-sm">
                   
          </span>
        </div>
      </div>

      {/*   */}
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-4">
            
        </h2>
        <p className="text-lg text-gray-600 mb-6">
               
        </p>
        
        {/*    */}
        <div className="inline-flex items-center bg-gray-100 rounded-lg p-1">
          <button
            onClick={() => setBillingCycle('monthly')}
            className={`px-4 py-2 rounded-md transition-colors ${
              billingCycle === 'monthly'
                ? 'bg-white text-gray-900 shadow-sm'
                : 'text-gray-600'
            }`}
          >
             
          </button>
          <button
            onClick={() => setBillingCycle('yearly')}
            className={`px-4 py-2 rounded-md transition-colors ${
              billingCycle === 'yearly'
                ? 'bg-white text-gray-900 shadow-sm'
                : 'text-gray-600'
            }`}
          >
             
            <span className="ml-2 text-xs bg-green-100 text-green-800 px-2 py-1 rounded">
              2 
            </span>
          </button>
        </div>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
        {paymentPlans.map((plan) => (
          <div
            key={plan.id}
            className={`relative bg-white rounded-lg shadow-lg overflow-hidden ${
              plan.popular ? 'ring-2 ring-blue-600' : ''
            } ${selectedPlan === plan.id ? 'transform scale-105' : ''} 
            transition-transform cursor-pointer hover:shadow-xl`}
            onClick={() => setSelectedPlan(plan.id)}
          >
            {plan.popular && (
              <div className="absolute top-0 right-0 bg-blue-600 text-white px-3 py-1 text-xs font-semibold">
                
              </div>
            )}
            
            <div className="p-6">
              <h3 className="text-xl font-semibold text-gray-900 mb-2">
                {plan.name}
              </h3>
              
              <div className="mb-4">
                {plan.originalPrice && billingCycle === 'monthly' && (
                  <span className="text-gray-400 line-through text-sm">
                    {plan.originalPrice.toLocaleString()}
                  </span>
                )}
                <div className="flex items-baseline">
                  <span className="text-3xl font-bold text-gray-900">
                    {plan.price === 0 ? '' : `${getDiscountedPrice(plan.price).toLocaleString()}`}
                  </span>
                  {plan.price > 0 && (
                    <span className="text-gray-500 ml-2">/{plan.period}</span>
                  )}
                </div>
              </div>

              <ul className="space-y-3 mb-6">
                <li className="flex items-center text-sm">
                  <span className="text-green-500 mr-2"></span>
                  <span>
                     {plan.features.projects === -1 ? '' : `${plan.features.projects}`}
                  </span>
                </li>
                <li className="flex items-center text-sm">
                  <span className="text-green-500 mr-2"></span>
                  <span>
                     {plan.features.users === -1 ? '' : `${plan.features.users}`}
                  </span>
                </li>
                <li className="flex items-center text-sm">
                  <span className="text-green-500 mr-2"></span>
                  <span>
                     {plan.features.evaluators === -1 ? '' : `${plan.features.evaluators}`}
                  </span>
                </li>
                <li className="flex items-center text-sm">
                  <span className="text-green-500 mr-2"></span>
                  <span>{plan.features.support}</span>
                </li>
                <li className="flex items-center text-sm">
                  <span className={`mr-2 ${plan.features.export ? 'text-green-500' : 'text-gray-300'}`}>
                    {plan.features.export ? '' : ''}
                  </span>
                  <span className={plan.features.export ? '' : 'text-gray-400'}>
                     
                  </span>
                </li>
                <li className="flex items-center text-sm">
                  <span className={`mr-2 ${plan.features.api ? 'text-green-500' : 'text-gray-300'}`}>
                    {plan.features.api ? '' : ''}
                  </span>
                  <span className={plan.features.api ? '' : 'text-gray-400'}>
                    API 
                  </span>
                </li>
              </ul>

              <button
                className="w-full px-4 py-2 rounded-lg font-medium bg-gray-100 text-gray-400 cursor-not-allowed"
                disabled
                onClick={(e) => e.stopPropagation()}
              >
                  
              </button>
            </div>
          </div>
        ))}
      </div>

      {/*    */}
      {selectedPlan !== 'free' && (
        <Card title=" ">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/*    */}
            <div>
              <h4 className="font-semibold mb-4"> </h4>
              <div className="space-y-3">
                {/* KG   ( ) */}
                {[
                  { id: 'card', name: '/', icon: '', available: true },
                  { id: 'kakao', name: '', icon: '', available: false },
                  { id: 'naver', name: '', icon: '', available: false },
                  { id: 'toss', name: '', icon: '', available: false },
                  { id: 'transfer', name: '', icon: '', available: false }
                ].map((method) => (
                  <div
                    key={method.id}
                    className={`flex items-center justify-between p-3 border rounded-lg ${
                      method.available
                        ? 'border-blue-200 bg-blue-50'
                        : 'border-gray-100 bg-gray-50 opacity-60'
                    }`}
                  >
                    <div className="flex items-center">
                      <input
                        type="radio"
                        name="paymentMethod"
                        value={method.id}
                        checked={paymentMethod === method.id}
                        onChange={(e) => setPaymentMethod(e.target.value)}
                        disabled={!method.available}
                        className="mr-3"
                      />
                      <span className="text-xl mr-3">{method.icon}</span>
                      <span className={`font-medium ${!method.available ? 'text-gray-400' : ''}`}>
                        {method.name}
                      </span>
                    </div>
                    {method.available
                      ? <span className="text-xs text-blue-600 font-medium bg-blue-100 px-2 py-0.5 rounded">KG</span>
                      : <span className="text-xs text-gray-400"> </span>
                    }
                  </div>
                ))}
              </div>
              <p className="mt-3 text-xs text-gray-500">
                * KG      
              </p>
            </div>

            {/*   */}
            <div>
              <h4 className="font-semibold mb-4"> </h4>
              <div className="bg-gray-50 rounded-lg p-6">
                <div className="space-y-3">
                  <div className="flex justify-between">
                    <span className="text-gray-600"> </span>
                    <span className="font-medium">
                      {paymentPlans.find(p => p.id === selectedPlan)?.name}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600"> </span>
                    <span className="font-medium">
                      {billingCycle === 'monthly' ? '' : ''}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600"> </span>
                    <span className="font-medium">
                      {(paymentPlans.find(p => p.id === selectedPlan)?.price || 0).toLocaleString()}
                    </span>
                  </div>
                  {billingCycle === 'yearly' && (
                    <div className="flex justify-between text-green-600">
                      <span>  (2 )</span>
                      <span className="font-medium">
                        -{((paymentPlans.find(p => p.id === selectedPlan)?.price || 0) * 2).toLocaleString()}
                      </span>
                    </div>
                  )}
                  <div className="border-t pt-3">
                    <div className="flex justify-between text-lg font-bold">
                      <span>  </span>
                      <span className="text-blue-600">
                        {getDiscountedPrice(
                          paymentPlans.find(p => p.id === selectedPlan)?.price || 0
                        ).toLocaleString()}
                        {billingCycle === 'yearly' && '/'}
                      </span>
                    </div>
                  </div>
                </div>

                <div className="mt-6">
                  <button
                    disabled
                    className="w-full px-4 py-3 rounded-lg font-semibold bg-gray-200 text-gray-400 cursor-not-allowed"
                  >
                        
                  </button>
                  <p className="text-xs text-gray-500 text-center mt-3">
                      PG    
                  </p>
                </div>
              </div>

              {/*    */}
              <div className="mt-4 p-4 bg-green-50 rounded-lg">
                <div className="flex items-center">
                  <span className="text-green-600 text-xl mr-3"></span>
                  <div>
                    <h5 className="font-medium text-green-900">KG  </h5>
                    <p className="text-sm text-green-700">
                         KG PG  SSL   
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Card>
      )}

      {/* FAQ  */}
      <div className="mt-12">
        <h3 className="text-xl font-semibold mb-6">  </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-gray-50 rounded-lg p-6">
            <h4 className="font-medium mb-2">    ?</h4>
            <p className="text-sm text-gray-600">
              ,       . 
                  .
            </p>
          </div>
          <div className="bg-gray-50 rounded-lg p-6">
            <h4 className="font-medium mb-2">   ?</h4>
            <p className="text-sm text-gray-600">
                 7    , 
                     .
            </p>
          </div>
          <div className="bg-gray-50 rounded-lg p-6">
            <h4 className="font-medium mb-2">   ?</h4>
            <p className="text-sm text-gray-600">
              Enterprise        
              .   .
            </p>
          </div>
          <div className="bg-gray-50 rounded-lg p-6">
            <h4 className="font-medium mb-2">  ?</h4>
            <p className="text-sm text-gray-600">
                 ,   
              2025    .
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PaymentSystem;