import React, { useState, useEffect } from 'react';
import Card from '../common/Card';
import Button from '../common/Button';
import Modal from '../common/Modal';

interface TrashBinProps {
  onFetchTrashedProjects?: () => Promise<any[]>;
  onRestoreProject?: (projectId: string) => Promise<any>;
  onPermanentDeleteProject?: (projectId: string) => Promise<any>;
  onBack?: () => void;
}

interface TrashedProject {
  id: string;
  title: string;
  description: string;
  created_at: string;
  deleted_at: string;
  admin_name?: string;
}

const TrashBin: React.FC<TrashBinProps> = ({
  onFetchTrashedProjects,
  onRestoreProject,
  onPermanentDeleteProject,
  onBack
}) => {
  const [trashedProjects, setTrashedProjects] = useState<TrashedProject[]>([]);
  const [loading, setLoading] = useState(true);
  const [actionLoading, setActionLoading] = useState<string | null>(null);
  const [debugInfo, setDebugInfo] = useState<string>('');
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);
  const [pendingAction, setPendingAction] = useState<{type: 'restore' | 'permanentDelete', id: string, title: string} | null>(null);
  const [permanentDeleteStep, setPermanentDeleteStep] = useState<1 | 2>(1);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  useEffect(() => {
    loadTrashedProjects();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadTrashedProjects = async () => {
    const debugLog = [];
    
    if (!onFetchTrashedProjects) {
      const msg = ' onFetchTrashedProjects   ';
      debugLog.push(msg);
      setDebugInfo(debugLog.join('\n'));
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      const startMsg = '    ...';
      debugLog.push(startMsg);

      const projects = await onFetchTrashedProjects();
      const resultMsg = `    : ${projects?.length || 0}`;
      debugLog.push(resultMsg);
      
      if (projects && projects.length > 0) {
        debugLog.push(`   : ${JSON.stringify(projects[0], null, 2)}`);
      } else {
        debugLog.push('  ');
      }
      
      setTrashedProjects(projects || []);
      setDebugInfo(debugLog.join('\n'));
    } catch (error) {
      const errorMsg = `    : ${error}`;
      debugLog.push(errorMsg);
      setDebugInfo(debugLog.join('\n'));
    } finally {
      setLoading(false);
    }
  };

  //  - trigger
  const handleRestore = (projectId: string, projectTitle: string) => {
    if (!onRestoreProject) return;
    setPendingAction({ type: 'restore', id: projectId, title: projectTitle });
  };

  //  - confirm
  const confirmRestore = async () => {
    if (!pendingAction || !onRestoreProject) return;
    const { id } = pendingAction;
    setPendingAction(null);
    try {
      setActionLoading(id);
      await onRestoreProject(id);
      setTrashedProjects(prev => prev.filter(p => p.id !== id));
      showActionMessage('success', '  .');
    } catch (error) {
      showActionMessage('error', '  .');
    } finally {
      setActionLoading(null);
    }
  };

  //   - trigger
  const handlePermanentDelete = (projectId: string, projectTitle: string) => {
    if (!onPermanentDeleteProject) return;
    setPermanentDeleteStep(1);
    setPendingAction({ type: 'permanentDelete', id: projectId, title: projectTitle });
  };

  //   - confirm (two-step)
  const confirmPermanentDelete = async () => {
    if (!pendingAction || !onPermanentDeleteProject) return;
    if (permanentDeleteStep === 1) {
      setPermanentDeleteStep(2);
      return;
    }
    const { id } = pendingAction;
    setPendingAction(null);
    setPermanentDeleteStep(1);
    try {
      setActionLoading(id);
      await onPermanentDeleteProject(id);
      setTrashedProjects(prev => prev.filter(p => p.id !== id));
      showActionMessage('success', '  .');
    } catch (error) {
      showActionMessage('error', '  .');
    } finally {
      setActionLoading(null);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getTimeAgo = (dateString: string) => {
    const now = new Date();
    const date = new Date(dateString);
    const diffMs = now.getTime() - date.getTime();
    const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
    
    if (diffDays === 0) return '';
    if (diffDays === 1) return '1 ';
    if (diffDays < 7) return `${diffDays} `;
    if (diffDays < 30) return `${Math.floor(diffDays / 7)} `;
    return `${Math.floor(diffDays / 30)} `;
  };

  return (
    <div className="max-w-6xl mx-auto space-y-6">
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}
      {/*  */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2 flex items-center">
             
          </h1>
          <p className="text-gray-600">
                  .
          </p>
        </div>
        {onBack && (
          <Button variant="outline" onClick={onBack}>
             
          </Button>
        )}
      </div>

      {/*   */}
      <Card variant="outlined" className="bg-amber-50 border-amber-200">
        <div className="flex items-start space-x-3">
          <span className="text-2xl"></span>
          <div>
            <h3 className="font-medium text-amber-800 mb-2">  </h3>
            <ul className="text-sm text-amber-700 space-y-1">
              <li> <strong></strong>:    </li>
              <li> <strong> </strong>:     ( )</li>
              <li> 30    </li>
            </ul>
          </div>
        </div>
      </Card>

      {/*    */}
      {debugInfo && (
        <Card variant="outlined" className="bg-blue-50 border-blue-200">
          <div className="flex items-start space-x-3">
            <span className="text-2xl"></span>
            <div>
              <h3 className="font-medium text-blue-800 mb-2">  </h3>
              <pre className="text-xs text-blue-700 whitespace-pre-wrap">{debugInfo}</pre>
            </div>
          </div>
        </Card>
      )}

      {/*   */}
      <Card>
        {loading ? (
          <div className="text-center py-12">
            <div className="text-4xl mb-4"></div>
            <h3 className="text-xl font-medium text-gray-700 mb-2">  ...</h3>
            <p className="text-gray-500">   .</p>
          </div>
        ) : trashedProjects.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-xl font-medium text-gray-700 mb-2"> </h3>
            <p className="text-gray-500">  .</p>
          </div>
        ) : (
          <div className="space-y-4">
            <div className="flex items-center justify-between border-b pb-4">
              <h2 className="text-lg font-medium">  ({trashedProjects.length})</h2>
            </div>

            <div className="space-y-4">
              {trashedProjects.map((project) => (
                <div 
                  key={project.id}
                  className="border border-gray-200 rounded-lg p-4 bg-gray-50 hover:bg-gray-100 transition-colors"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className="font-medium text-gray-900 mb-1">
                        {project.title}
                      </h3>
                      <p className="text-sm text-gray-600 mb-3">
                        {project.description}
                      </p>
                      <div className="flex items-center space-x-4 text-xs text-gray-500">
                        <span>: {formatDate(project.created_at)}</span>
                        <span></span>
                        <span>: {formatDate(project.deleted_at)} ({getTimeAgo(project.deleted_at)})</span>
                        {project.admin_name && (
                          <>
                            <span></span>
                            <span>: {project.admin_name}</span>
                          </>
                        )}
                      </div>
                    </div>

                    <div className="flex items-center space-x-2 ml-4">
                      <Button
                        variant="primary"
                        size="sm"
                        onClick={() => handleRestore(project.id, project.title)}
                        disabled={actionLoading === project.id}
                        loading={actionLoading === project.id}
                      >
                         
                      </Button>
                      <Button
                        variant="error"
                        size="sm"
                        onClick={() => handlePermanentDelete(project.id, project.title)}
                        disabled={actionLoading === project.id}
                      >
                         
                      </Button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </Card>
      {/*   */}
      <Modal
        isOpen={pendingAction !== null}
        onClose={() => { setPendingAction(null); setPermanentDeleteStep(1); }}
        title={pendingAction?.type === 'restore' ? ' ' : '  '}
        size="sm"
        footer={
          <div className="flex justify-end space-x-2">
            <button
              onClick={() => { setPendingAction(null); setPermanentDeleteStep(1); }}
              className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
            >
              
            </button>
            <button
              onClick={pendingAction?.type === 'restore' ? confirmRestore : confirmPermanentDelete}
              className={`px-4 py-2 text-sm font-medium text-white rounded-md ${
                pendingAction?.type === 'permanentDelete'
                  ? 'bg-red-600 hover:bg-red-700'
                  : 'bg-green-600 hover:bg-green-700'
              }`}
            >
              {pendingAction?.type === 'restore' ? '' :
               permanentDeleteStep === 1 ? '' : ' '}
            </button>
          </div>
        }
      >
        {pendingAction?.type === 'restore' && (
          <p className="text-sm text-gray-700">
            <strong>"{pendingAction.title}"</strong>  ?
          </p>
        )}
        {pendingAction?.type === 'permanentDelete' && permanentDeleteStep === 1 && (
          <div className="space-y-2">
            <p className="text-sm text-gray-700">
              <strong>"{pendingAction.title}"</strong>   ?
            </p>
            <p className="text-sm text-red-600 font-medium">     !</p>
            <ul className="text-sm text-gray-600 list-disc list-inside space-y-1">
              <li>  </li>
              <li>  </li>
              <li>  </li>
            </ul>
            <p className="text-sm text-gray-600">   .</p>
          </div>
        )}
        {pendingAction?.type === 'permanentDelete' && permanentDeleteStep === 2 && (
          <div className="space-y-2">
            <p className="text-sm text-gray-700">
               <strong>"{pendingAction.title}"</strong>  ?
            </p>
            <p className="text-sm text-red-600 font-medium"> .</p>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default TrashBin;