import React, { useState } from 'react';

interface Question {
  id: string;
  type: 'text' | 'select' | 'radio' | 'checkbox' | 'textarea' | 'number' | 'date';
  question: string;
  options?: string[];
  required: boolean;
}

interface InitialSurveyData {
  title?: string;
  description?: string;
  questions?: Question[];
}

interface SurveyFormBuilderProps {
  onSave?: (questions: Question[], metadata?: { title: string; description: string }) => void;
  onCancel?: () => void;
  initialSurvey?: InitialSurveyData; //    
}

const SurveyFormBuilder: React.FC<SurveyFormBuilderProps> = ({ onSave, onCancel, initialSurvey }) => {
  const [surveyTitle, setSurveyTitle] = useState(initialSurvey?.title || ' ');
  const [surveyDescription, setSurveyDescription] = useState(initialSurvey?.description || '');
  const [questions, setQuestions] = useState<Question[]>(initialSurvey?.questions || []);
  const [isPreview, setIsPreview] = useState(false);
  const [showTemplateSelection, setShowTemplateSelection] = useState(!initialSurvey); //     
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  //   
  const demographicTemplates = {
    standard: {
      name: '  ',
      description: '     ',
      questions: [
        {
          id: '1',
          type: 'radio' as const,
          question: ' ',
          options: ['20-29', '30-39', '40-49', '50 '],
          required: true
        },
        {
          id: '2',
          type: 'radio' as const,
          question: ' ',
          options: ['', ''],
          required: true
        },
        {
          id: '3',
          type: 'radio' as const,
          question: '  ',
          options: [' ', ' ', '4  ', '  ()', '  ()'],
          required: true
        },
        {
          id: '4',
          type: 'radio' as const,
          question: '   ',
          options: ['IT/', '', '', '', '', '', '', ''],
          required: true
        },
        {
          id: '5',
          type: 'radio' as const,
          question: '   ',
          options: ['5 ', '5-10', '10-15', '15 '],
          required: true
        },
        {
          id: '6',
          type: 'text' as const,
          question: 'AHP      ',
          options: [],
          required: true
        }
      ]
    },
    academic: {
      name: '  ',
      description: '      ',
      questions: [
        {
          id: '1',
          type: 'radio' as const,
          question: ' ',
          options: ['20-29', '30-39', '40-49', '50 '],
          required: true
        },
        {
          id: '2',
          type: 'radio' as const,
          question: ' ',
          options: ['', ''],
          required: true
        },
        {
          id: '3',
          type: 'radio' as const,
          question: '  ',
          options: ['', '', ''],
          required: true
        },
        {
          id: '4',
          type: 'radio' as const,
          question: '  ',
          options: ['', '', '', '', '', '', '', ''],
          required: true
        },
        {
          id: '5',
          type: 'radio' as const,
          question: '  ',
          options: ['', '', '/', ' ', ' '],
          required: true
        },
        {
          id: '6',
          type: 'radio' as const,
          question: 'AHP   ',
          options: ['', '1-2', '3-5', '5 '],
          required: true
        },
        {
          id: '7',
          type: 'text' as const,
          question: 'AHP      ',
          options: [],
          required: true
        }
      ]
    },
    business: {
      name: '  ',
      description: '      ',
      questions: [
        {
          id: '1',
          type: 'radio' as const,
          question: ' ',
          options: ['20-29', '30-39', '40-49', '50 '],
          required: true
        },
        {
          id: '2',
          type: 'radio' as const,
          question: ' ',
          options: ['', ''],
          required: true
        },
        {
          id: '3',
          type: 'radio' as const,
          question: ' ',
          options: ['', '', '', '', '', ''],
          required: true
        },
        {
          id: '4',
          type: 'radio' as const,
          question: ' ',
          options: ['', '', '', '', '', '', '', ''],
          required: true
        },
        {
          id: '5',
          type: 'radio' as const,
          question: ' ',
          options: ['3 ', '3-7', '7-15', '15 '],
          required: true
        },
        {
          id: '6',
          type: 'radio' as const,
          question: '  ',
          options: ['  ', '  ', '  ', '  '],
          required: true
        },
        {
          id: '7',
          type: 'text' as const,
          question: 'AHP      ',
          options: [],
          required: true
        }
      ]
    }
  };

  //  
  const applyTemplate = (templateKey: keyof typeof demographicTemplates) => {
    const template = demographicTemplates[templateKey];
    setQuestions(template.questions);
    setSurveyTitle(template.name);
    setSurveyDescription(template.description);
    setShowTemplateSelection(false);
  };

  const questionTypes = [
    { value: 'text', label: ' ', icon: '' },
    { value: 'textarea', label: ' ', icon: '' },
    { value: 'select', label: '', icon: '' },
    { value: 'radio', label: ' ()', icon: '' },
    { value: 'checkbox', label: ' ()', icon: '' },
    { value: 'number', label: '', icon: '' },
    { value: 'date', label: '', icon: '' }
  ];

  //   
  const renderTemplateSelection = () => (
    <div className="max-w-4xl mx-auto p-6">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold mb-4" style={{ color: 'var(--text-primary)' }}>
             
        </h2>
        <p className="text-lg" style={{ color: 'var(--text-secondary)' }}>
                  
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        {Object.entries(demographicTemplates).map(([key, template]) => (
          <div 
            key={key}
            className="border-2 border-gray-200 rounded-xl p-6 hover:border-blue-400 transition-all cursor-pointer hover:shadow-lg"
            onClick={() => applyTemplate(key as keyof typeof demographicTemplates)}
          >
            <div className="text-center mb-4">
              <div className="text-4xl mb-3">
                {key === 'standard' ? '' : key === 'academic' ? '' : ''}
              </div>
              <h3 className="text-xl font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                {template.name}
              </h3>
              <p className="text-sm mb-4" style={{ color: 'var(--text-secondary)' }}>
                {template.description}
              </p>
            </div>
            
            <div className="text-left">
              <div className="text-sm font-medium mb-2 text-gray-600">  ({template.questions.length}):</div>
              <ul className="text-xs text-gray-500 space-y-1">
                {template.questions.slice(0, 3).map((q, i) => (
                  <li key={i}> {q.question}</li>
                ))}
                {template.questions.length > 3 && (
                  <li>  {template.questions.length - 3} </li>
                )}
              </ul>
            </div>
            
            <div className="mt-4 pt-4 border-t border-gray-200">
              <button className="w-full py-2 px-4 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors">
                  
              </button>
            </div>
          </div>
        ))}
      </div>

      <div className="text-center">
        <div className="inline-block p-6 border-2 border-dashed border-gray-300 rounded-xl hover:border-blue-400 transition-colors">
          <div className="text-4xl mb-3"></div>
          <h3 className="text-xl font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
              
          </h3>
          <p className="text-sm mb-4" style={{ color: 'var(--text-secondary)' }}>
                 
          </p>
          <button 
            onClick={() => {
              setQuestions([]);
              setShowTemplateSelection(false);
            }}
            className="py-2 px-6 border border-gray-400 rounded-lg hover:bg-gray-50 transition-colors"
          >
              
          </button>
        </div>
      </div>
    </div>
  );

  const addQuestion = () => {
    const newQuestion: Question = {
      id: Date.now().toString(),
      type: 'text',
      question: ' ',
      options: [' 1', ' 2'],
      required: false
    };
    setQuestions([...questions, newQuestion]);
  };

  const updateQuestion = (id: string, field: keyof Question, value: unknown) => {
    setQuestions(questions.map(q => 
      q.id === id ? { ...q, [field]: value } : q
    ));
  };

  const deleteQuestion = (id: string) => {
    setQuestions(questions.filter(q => q.id !== id));
  };

  const addOption = (questionId: string) => {
    setQuestions(questions.map(q => {
      if (q.id === questionId && q.options) {
        return { ...q, options: [...q.options, ` ${q.options.length + 1}`] };
      }
      return q;
    }));
  };

  const updateOption = (questionId: string, optionIndex: number, value: string) => {
    setQuestions(questions.map(q => {
      if (q.id === questionId && q.options) {
        const newOptions = [...q.options];
        newOptions[optionIndex] = value;
        return { ...q, options: newOptions };
      }
      return q;
    }));
  };

  const deleteOption = (questionId: string, optionIndex: number) => {
    setQuestions(questions.map(q => {
      if (q.id === questionId && q.options && q.options.length > 1) {
        const newOptions = q.options.filter((_, index) => index !== optionIndex);
        return { ...q, options: newOptions };
      }
      return q;
    }));
  };

  const moveQuestion = (id: string, direction: 'up' | 'down') => {
    const index = questions.findIndex(q => q.id === id);
    if ((direction === 'up' && index > 0) || (direction === 'down' && index < questions.length - 1)) {
      const newQuestions = [...questions];
      const newIndex = direction === 'up' ? index - 1 : index + 1;
      [newQuestions[index], newQuestions[newIndex]] = [newQuestions[newIndex], newQuestions[index]];
      setQuestions(newQuestions);
    }
  };

  const duplicateQuestion = (id: string) => {
    const questionToDuplicate = questions.find(q => q.id === id);
    if (questionToDuplicate) {
      const newQuestion = {
        ...questionToDuplicate,
        id: Date.now().toString(),
        question: `${questionToDuplicate.question} ()`
      };
      const index = questions.findIndex(q => q.id === id);
      const newQuestions = [...questions];
      newQuestions.splice(index + 1, 0, newQuestion);
      setQuestions(newQuestions);
    }
  };

  const handleSave = () => {
    if (onSave) {
      onSave(questions, { title: surveyTitle, description: surveyDescription });
    }
    showActionMessage('success', ' !');
  };

  const renderPreview = () => {
    return (
      <div className="max-w-3xl mx-auto p-6">
        <div className="bg-white rounded-lg shadow-lg p-8">
          <h2 className="text-3xl font-bold mb-2" style={{ color: 'var(--text-primary)' }}>
            {surveyTitle}
          </h2>
          {surveyDescription && (
            <p className="text-lg mb-8" style={{ color: 'var(--text-secondary)' }}>
              {surveyDescription}
            </p>
          )}
          
          <div className="space-y-6">
            {questions.map((question, index) => (
              <div key={question.id} className="p-6 border rounded-lg bg-gray-50">
                <div className="mb-4">
                  <label className="block font-medium mb-2" style={{ color: 'var(--text-primary)' }}>
                    {index + 1}. {question.question}
                    {question.required && <span className="text-red-500 ml-1">*</span>}
                  </label>
                  
                  {question.type === 'text' && (
                    <input type="text" className="w-full p-3 border rounded-lg" placeholder="  " />
                  )}
                  
                  {question.type === 'textarea' && (
                    <textarea className="w-full p-3 border rounded-lg" rows={4} placeholder="  " />
                  )}
                  
                  {question.type === 'select' && question.options && (
                    <select className="w-full p-3 border rounded-lg">
                      <option value=""></option>
                      {question.options.map((option, i) => (
                        <option key={i} value={option}>{option}</option>
                      ))}
                    </select>
                  )}
                  
                  {question.type === 'radio' && question.options && (
                    <div className="space-y-2">
                      {question.options.map((option, i) => (
                        <label key={i} className="flex items-center">
                          <input type="radio" name={`question-${question.id}`} className="mr-2" />
                          <span>{option}</span>
                        </label>
                      ))}
                    </div>
                  )}
                  
                  {question.type === 'checkbox' && question.options && (
                    <div className="space-y-2">
                      {question.options.map((option, i) => (
                        <label key={i} className="flex items-center">
                          <input type="checkbox" className="mr-2" />
                          <span>{option}</span>
                        </label>
                      ))}
                    </div>
                  )}
                  
                  {question.type === 'number' && (
                    <input type="number" className="w-full p-3 border rounded-lg" placeholder=" " />
                  )}
                  
                  {question.type === 'date' && (
                    <input type="date" className="w-full p-3 border rounded-lg" />
                  )}
                </div>
              </div>
            ))}
          </div>
          
          <div className="mt-8 flex justify-end space-x-4">
            <button
              onClick={() => setIsPreview(false)}
              className="px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50"
            >
               
            </button>
            <button
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
            >
              
            </button>
          </div>
        </div>
      </div>
    );
  };

  if (isPreview) {
    return renderPreview();
  }

  if (showTemplateSelection) {
    return renderTemplateSelection();
  }

  return (
    <div className="max-w-6xl mx-auto p-6">
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}
      <div className="backdrop-blur-xl rounded-2xl p-8 shadow-lg" style={{
        backgroundColor: 'var(--card-bg)', 
        border: '1px solid var(--card-border)' 
      }}>
        {/*  */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h2 className="text-2xl font-bold mb-2" style={{ color: 'var(--text-primary)' }}>
              {initialSurvey ? '  ' : '   '}
            </h2>
            <p style={{ color: 'var(--text-secondary)' }}>
              {initialSurvey ? '      ' : '        '}
            </p>
          </div>
          <div className="flex space-x-3">
            {!initialSurvey && (
              <button
                onClick={() => setShowTemplateSelection(true)}
                className="px-4 py-2 border rounded-lg transition-colors"
                style={{ 
                  borderColor: 'var(--border-default)', 
                  color: 'var(--text-secondary)',
                  backgroundColor: 'transparent'
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.backgroundColor = 'var(--bg-elevated)';
                  e.currentTarget.style.borderColor = 'var(--accent-primary)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.backgroundColor = 'transparent';
                  e.currentTarget.style.borderColor = 'var(--border-default)';
                }}
              >
                  
              </button>
            )}
            <button
              onClick={() => setIsPreview(true)}
              className="px-4 py-2 border rounded-lg transition-colors"
              style={{ 
                borderColor: 'var(--accent-primary)', 
                color: 'var(--accent-primary)',
                backgroundColor: 'transparent'
              }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-light)'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
            >
               
            </button>
            <button
              onClick={handleSave}
              className="px-4 py-2 rounded-lg transition-colors"
              style={{ 
                backgroundColor: 'var(--accent-primary)',
                color: 'white',
                border: '1px solid var(--accent-primary)'
              }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-hover)'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-primary)'}
            >
               
            </button>
          </div>
        </div>

        {/*     */}
        <div className="mb-8 p-6 bg-gray-50 rounded-lg">
          <input
            type="text"
            value={surveyTitle}
            onChange={(e) => setSurveyTitle(e.target.value)}
            className="w-full text-2xl font-bold p-3 mb-4 border-b-2 border-gray-300 bg-transparent focus:border-blue-500 focus:outline-none"
            placeholder=" "
          />
          <textarea
            value={surveyDescription}
            onChange={(e) => setSurveyDescription(e.target.value)}
            className="w-full p-3 bg-transparent border-b border-gray-300 focus:border-blue-500 focus:outline-none resize-none"
            placeholder="  ()"
            rows={2}
          />
        </div>

        {/*   */}
        <div className="space-y-4 mb-6">
          {questions.length === 0 ? (
            <div className="text-center py-12 border-2 border-dashed border-gray-300 rounded-lg">
              <div className="text-4xl mb-4"></div>
              <h3 className="text-xl font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                 
              </h3>
              <p className="mb-6" style={{ color: 'var(--text-secondary)' }}>
                      
              </p>
              <div className="flex justify-center space-x-4">
                <button
                  onClick={() => setShowTemplateSelection(true)}
                  className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                >
                    
                </button>
                <button
                  onClick={addQuestion}
                  className="px-6 py-3 border border-gray-400 rounded-lg hover:bg-gray-50 transition-colors"
                >
                     
                </button>
              </div>
            </div>
          ) : (
            questions.map((question, index) => (
            <div key={question.id} className="p-6 border-2 border-gray-200 rounded-lg hover:border-blue-300 transition-colors">
              {/*   */}
              <div className="flex items-start justify-between mb-4">
                <div className="flex-1 mr-4">
                  <div className="flex items-center mb-3">
                    <span className="text-lg font-semibold mr-3" style={{ color: 'var(--text-muted)' }}>
                      {index + 1}.
                    </span>
                    <input
                      type="text"
                      value={question.question}
                      onChange={(e) => updateQuestion(question.id, 'question', e.target.value)}
                      className="flex-1 text-lg p-2 border-b focus:outline-none transition-colors"
                      style={{ 
                        borderColor: 'var(--border-default)', 
                        backgroundColor: 'transparent',
                        color: 'var(--text-primary)'
                      }}
                      onFocus={(e) => e.currentTarget.style.borderColor = 'var(--accent-primary)'}
                      onBlur={(e) => e.currentTarget.style.borderColor = 'var(--border-default)'}
                      placeholder=" "
                    />
                  </div>
                  
                  {/*    */}
                  <div className="flex items-center space-x-4">
                    <select
                      value={question.type}
                      onChange={(e) => updateQuestion(question.id, 'type', e.target.value)}
                      className="p-2 border rounded-lg focus:outline-none transition-colors"
                      style={{ 
                        borderColor: 'var(--border-default)',
                        backgroundColor: 'var(--input-bg)',
                        color: 'var(--text-primary)'
                      }}
                      onFocus={(e) => e.currentTarget.style.borderColor = 'var(--accent-primary)'}
                      onBlur={(e) => e.currentTarget.style.borderColor = 'var(--border-default)'}
                    >
                      {questionTypes.map(type => (
                        <option key={type.value} value={type.value}>
                          {type.icon} {type.label}
                        </option>
                      ))}
                    </select>
                    
                    <label className="flex items-center">
                      <input
                        type="checkbox"
                        checked={question.required}
                        onChange={(e) => updateQuestion(question.id, 'required', e.target.checked)}
                        className="mr-2"
                      />
                      <span className="text-sm"> </span>
                    </label>
                  </div>
                </div>
                
                {/*   */}
                <div className="flex items-center space-x-2">
                  <button
                    onClick={() => moveQuestion(question.id, 'up')}
                    disabled={index === 0}
                    className="p-2 hover:bg-gray-100 rounded disabled:opacity-50"
                    title=" "
                  >
                    
                  </button>
                  <button
                    onClick={() => moveQuestion(question.id, 'down')}
                    disabled={index === questions.length - 1}
                    className="p-2 hover:bg-gray-100 rounded disabled:opacity-50"
                    title=" "
                  >
                    
                  </button>
                  <button
                    onClick={() => duplicateQuestion(question.id)}
                    className="p-2 hover:bg-gray-100 rounded"
                    title=""
                  >
                    
                  </button>
                  <button
                    onClick={() => deleteQuestion(question.id)}
                    className="p-2 hover:bg-red-100 rounded text-red-500"
                    title=""
                  >
                    
                  </button>
                </div>
              </div>
              
              {/*   (select, radio, checkbox) */}
              {['select', 'radio', 'checkbox'].includes(question.type) && question.options && (
                <div className="mt-4 pl-8">
                  <div className="space-y-2">
                    {question.options.map((option, optionIndex) => (
                      <div key={optionIndex} className="flex items-center space-x-2">
                        <span className="text-gray-400">
                          {question.type === 'radio' ? '' : question.type === 'checkbox' ? '' : ''}
                        </span>
                        <input
                          type="text"
                          value={option}
                          onChange={(e) => updateOption(question.id, optionIndex, e.target.value)}
                          className="flex-1 p-2 border-b border-gray-200 focus:border-blue-500 focus:outline-none"
                          placeholder=" "
                        />
                        {question.options && question.options.length > 1 && (
                          <button
                            onClick={() => deleteOption(question.id, optionIndex)}
                            className="p-1 text-red-500 hover:bg-red-100 rounded"
                          >
                            
                          </button>
                        )}
                      </div>
                    ))}
                  </div>
                  <button
                    onClick={() => addOption(question.id)}
                    className="mt-3 text-blue-600 hover:text-blue-700 text-sm font-medium"
                  >
                    +  
                  </button>
                </div>
              )}
            </div>
            ))
          )}
        </div>

        {/*    */}
        {questions.length > 0 && (
          <button
            onClick={addQuestion}
            className="w-full p-4 border-2 border-dashed border-gray-300 rounded-lg hover:border-blue-400 hover:bg-blue-50 transition-colors"
          >
            <span className="text-blue-600 font-medium">   </span>
          </button>
        )}

        {/*   */}
        <div className="flex justify-end space-x-4 mt-8 pt-6 border-t">
          {onCancel && (
            <button
              onClick={onCancel}
              className="px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50"
            >
              
            </button>
          )}
          <button
            onClick={() => setIsPreview(true)}
            className="px-6 py-3 border rounded-lg transition-colors"
            style={{ 
              borderColor: 'var(--accent-primary)', 
              color: 'var(--accent-primary)',
              backgroundColor: 'transparent'
            }}
            onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-light)'}
            onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'transparent'}
          >
            
          </button>
          <button
            onClick={handleSave}
            className="px-6 py-3 rounded-lg transition-colors"
            style={{ 
              backgroundColor: 'var(--accent-primary)',
              color: 'white',
              border: '1px solid var(--accent-primary)'
            }}
            onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-hover)'}
            onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-primary)'}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default SurveyFormBuilder;