import React, { useState, useEffect, useCallback } from 'react';
import dataService from '../../services/dataService_clean';
import { ProjectData } from '../../services/api';
import Modal from '../common/Modal';


interface MyProjectsProps {
  onProjectSelect?: (project: ProjectData) => void;
  onCreateNew?: () => void;
  onEditProject?: (project: ProjectData) => void;
  onDeleteProject?: (projectId: string) => void;
  onModelBuilder?: (project: ProjectData) => void;
  onAnalysis?: (project: ProjectData) => void;
  refreshTrigger?: number; //     
}

const MyProjects: React.FC<MyProjectsProps> = ({ 
  onProjectSelect, 
  onCreateNew, 
  onEditProject, 
  onDeleteProject, 
  onModelBuilder, 
  onAnalysis,
  refreshTrigger
}) => {
  const [projects, setProjects] = useState<ProjectData[]>([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState<'all' | 'active' | 'completed' | 'draft' | 'trash'>('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);
  const [pendingAction, setPendingAction] = useState<{type: 'restore' | 'permanentDelete' | 'trash', id: string, title: string} | null>(null);
  const [permanentDeleteStep, setPermanentDeleteStep] = useState<1 | 2>(1);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  const fetchProjects = useCallback(async () => {
    try {
      setLoading(true);
      let data;
      if (filter === 'trash') {
        //   
        data = await dataService.getTrashedProjects();
      } else {
        //   
        data = await dataService.getProjects();
      }
      
      //      (API  )
      if (data && Array.isArray(data)) {
        const projectsWithStats = data.map((project) => {
          //   ( )
          let completionRate = 0;
          const criteriaCount = project.criteria_count || 0;
          const alternativesCount = project.alternatives_count || 0;
          
          if (project.status === 'completed') {
            completionRate = 100;
          } else if (project.status === 'active') {
            completionRate = Math.min(80, (criteriaCount * 20) + (alternativesCount * 15));
          } else if (criteriaCount > 0 || alternativesCount > 0) {
            completionRate = Math.min(50, (criteriaCount * 10) + (alternativesCount * 8));
          }
          
          return {
            ...project,
            criteria_count: criteriaCount,
            alternatives_count: alternativesCount,
            completionRate: completionRate
          };
        });
        setProjects(projectsWithStats);
      } else {
        setProjects([]);
      }
    } catch (error) {
      setProjects([]);
    } finally {
      setLoading(false);
    }
  }, [filter]);

  useEffect(() => {
    fetchProjects();
  }, [fetchProjects]);

  // refreshTrigger    
  useEffect(() => {
    if (refreshTrigger !== undefined) {
      fetchProjects();
    }
  }, [refreshTrigger, fetchProjects]);

  // filter    
  useEffect(() => {
    fetchProjects();
  }, [filter, fetchProjects]);

  const filteredProjects = projects.filter(project => {
    //      ( fetchProjects )
    if (filter === 'trash') return true;
    
    //  
    if (filter !== 'all' && project.status !== filter) return false;
    if (searchTerm && !project.title.toLowerCase().includes(searchTerm.toLowerCase())) return false;
    return true;
  });

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return { backgroundColor: 'var(--accent-secondary-pastel)', color: 'var(--accent-secondary-dark)' };
      case 'completed': return { backgroundColor: 'var(--accent-primary-pastel)', color: 'var(--accent-primary-dark)' };
      case 'draft': return { backgroundColor: 'var(--bg-subtle)', color: 'var(--text-secondary)' };
      default: return { backgroundColor: 'var(--bg-subtle)', color: 'var(--text-secondary)' };
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'active': return '';
      case 'completed': return '';
      case 'draft': return '';
      default: return status;
    }
  };

  const getProgressColor = (percentage: number) => {
    if (percentage >= 80) return '#10B981'; // green
    if (percentage >= 50) return '#F59E0B'; // yellow
    if (percentage >= 20) return '#3B82F6'; // blue
    return '#6B7280'; // gray
  };

  const getProgressText = (project: ProjectData) => {
    const criteriaCount = project.criteria_count || 0;
    const alternativesCount = project.alternatives_count || 0;
    const completionRate = project.completionRate || 0;

    if (completionRate === 0) {
      return '  ';
    } else if (completionRate === 100) {
      return ' ';
    } else if (criteriaCount === 0) {
      return '  ';
    } else if (alternativesCount === 0) {
      return '  ';
    } else if (completionRate < 50) {
      return '   ';
    } else if (completionRate < 80) {
      return '  ';
    } else {
      return '  ';
    }
  };

  //    - trigger
  const handleRestoreProject = (projectId: string, projectTitle: string) => {
    setPendingAction({ type: 'restore', id: projectId, title: projectTitle });
  };

  //    - confirm
  const confirmRestoreProject = async () => {
    if (!pendingAction) return;
    const { id } = pendingAction;
    setPendingAction(null);
    try {
      const success = await dataService.restoreProject(id);
      if (success) {
        showActionMessage('success', '  .');
        fetchProjects();
      } else {
        showActionMessage('error', '  .');
      }
    } catch (error) {
      showActionMessage('error', '    .');
    }
  };

  //    - trigger
  const handlePermanentDelete = (projectId: string, projectTitle: string) => {
    setPermanentDeleteStep(1);
    setPendingAction({ type: 'permanentDelete', id: projectId, title: projectTitle });
  };

  //    - confirm (two-step)
  const confirmPermanentDelete = async () => {
    if (!pendingAction) return;
    if (permanentDeleteStep === 1) {
      setPermanentDeleteStep(2);
      return;
    }
    const { id } = pendingAction;
    setPendingAction(null);
    setPermanentDeleteStep(1);
    try {
      const success = await dataService.permanentDeleteProject(id);
      if (success) {
        showActionMessage('success', '  .');
        fetchProjects();
      } else {
        showActionMessage('error', '   .');
      }
    } catch (error) {
      showActionMessage('error', '    .');
    }
  };

  //    ( ) - trigger
  const handleDeleteWithConfirm = (project: ProjectData) => {
    const projectTitle = project.title || '';
    setPendingAction({ type: 'trash', id: project.id || '', title: projectTitle });
  };

  //    ( ) - confirm
  const confirmMoveToTrash = async () => {
    if (!pendingAction) return;
    const { id, title } = pendingAction;
    setPendingAction(null);
    try {
      if (onDeleteProject) {
        await onDeleteProject(id);
        fetchProjects();
      } else {
        const success = await dataService.deleteProject(id);
        if (success) {
          showActionMessage('success', `"${title}"  .`);
          fetchProjects();
        } else {
          showActionMessage('error', '  .');
        }
      }
    } catch (error) {
      showActionMessage('error', '    .');
    }
  };

  //  
  const handleEditProject = (project: ProjectData) => {
    if (onEditProject) {
      onEditProject(project);
    } else {
      showActionMessage('info', '   .');
    }
  };

  //  
  const handleModelBuilder = (project: ProjectData) => {
    if (onModelBuilder) {
      onModelBuilder(project);
    } else {
      showActionMessage('info', '    .');
    }
  };

  //  
  const handleAnalysis = (project: ProjectData) => {
    if (onAnalysis) {
      onAnalysis(project);
    } else {
      showActionMessage('info', '    .');
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="text-4xl mb-4" style={{ color: 'var(--text-muted)' }}></div>
          <p style={{ color: 'var(--text-secondary)' }}>  ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}
      {/*  */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold" style={{ color: 'var(--text-primary)' }}>
             
          </h2>
          <p className="text-sm mt-1" style={{ color: 'var(--text-secondary)' }}>
             {projects.length} 
          </p>
        </div>
        <button
          onClick={() => {
            if (onCreateNew) {
              onCreateNew();
            }
          }}
          className="px-4 py-2 rounded-lg flex items-center space-x-2 transition-colors"
          style={{ 
            backgroundColor: 'var(--accent-primary)', 
            color: 'white' 
          }}
          onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-hover)'}
          onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-primary)'}
        >
          <span>+</span>
          <span> </span>
        </button>
      </div>

      {/*    */}
      <div className="flex items-center space-x-4">
        <div className="flex-1">
          <input
            type="text"
            placeholder=" ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full p-3 border rounded-lg focus:outline-none"
            style={{
              borderColor: 'var(--border-light)',
              backgroundColor: 'var(--bg-primary)',
              color: 'var(--text-primary)'
            }}
            onFocus={(e) => e.currentTarget.style.borderColor = 'var(--accent-primary)'}
            onBlur={(e) => e.currentTarget.style.borderColor = 'var(--border-light)'}
          />
        </div>
        <div className="flex space-x-2">
          {(['all', 'active', 'completed', 'draft', 'trash'] as const).map(status => (
            <button
              key={status}
              onClick={() => setFilter(status)}
              className="px-4 py-2 rounded-lg transition-colors"
              style={filter === status 
                ? { backgroundColor: 'var(--accent-primary)', color: 'white' }
                : { backgroundColor: 'var(--bg-subtle)', color: 'var(--text-secondary)' }
              }
              onMouseEnter={(e) => {
                if (filter !== status) {
                  e.currentTarget.style.backgroundColor = 'var(--bg-elevated)';
                }
              }}
              onMouseLeave={(e) => {
                if (filter !== status) {
                  e.currentTarget.style.backgroundColor = 'var(--bg-subtle)';
                }
              }}
            >
              {status === 'all' ? '' : status === 'trash' ? ' ' : getStatusText(status)}
            </button>
          ))}
        </div>
      </div>

      {/*   */}
      {filteredProjects.length === 0 ? (
        <div className="text-center py-12 rounded-lg" style={{ backgroundColor: 'var(--bg-secondary)' }}>
          <div className="text-6xl mb-4" style={{ color: 'var(--text-muted)' }}></div>
          <p className="text-xl font-medium mb-2">
            {filter === 'trash' ? ' ' : ' '}
          </p>
          <p className="mb-6" style={{ color: 'var(--text-muted)' }}>
            {searchTerm ? '  ' : 
             filter === 'trash' ? '  ' :
             '   '}
          </p>
          {!searchTerm && (
            <button
              onClick={() => {
                if (onCreateNew) {
                  onCreateNew();
                }
              }}
              className="px-6 py-3 rounded-lg transition-colors"
              style={{ backgroundColor: 'var(--accent-primary)', color: 'white' }}
              onMouseEnter={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-hover)'}
              onMouseLeave={(e) => e.currentTarget.style.backgroundColor = 'var(--accent-primary)'}
            >
                
            </button>
          )}
        </div>
      ) : (
        <div className="max-w-5xl mx-auto">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {filteredProjects.map(project => (
            <div
              key={project.id}
              onClick={() => onProjectSelect?.(project)}
              className="p-6 rounded-xl transition-all cursor-pointer"
              style={{
                backgroundColor: 'var(--bg-primary)',
                border: '1px solid var(--border-light)',
                boxShadow: 'var(--shadow-sm)'
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.border = '1px solid var(--accent-primary)';
                e.currentTarget.style.boxShadow = 'var(--shadow-lg)';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.border = '1px solid var(--border-light)';
                e.currentTarget.style.boxShadow = 'var(--shadow-sm)';
              }}
            >
              {/*   */}
              <div className="flex items-start justify-between mb-4">
                <h3 className="font-bold text-lg flex-1 mr-2" style={{ color: 'var(--text-primary)' }}>
                  {project.title}
                </h3>
                <span className="px-2 py-1 text-xs rounded-full" style={getStatusColor(project.status || 'draft')}>
                  {getStatusText(project.status || 'draft')}
                </span>
              </div>

              {/*   */}
              {project.description && (
                <p className="text-sm mb-4 line-clamp-2" style={{ color: 'var(--text-secondary)' }}>
                  {project.description}
                </p>
              )}

              {/*    */}
              <div className="mb-3 px-3 py-2 rounded-lg" style={{ 
                backgroundColor: project.evaluation_mode === 'fuzzy_ahp' ? 'var(--color-purple-pastel-1)' : 'var(--color-gold-pastel-1)',
                border: `1px solid ${project.evaluation_mode === 'fuzzy_ahp' ? 'var(--color-purple-pastel-3)' : 'var(--color-gold-pastel-3)'}`
              }}>
                <div className="flex items-center justify-between">
                  <span className="text-xs font-medium" style={{ color: 'var(--text-secondary)' }}>
                     
                  </span>
                  <span className="text-sm font-semibold" style={{ 
                    color: project.evaluation_mode === 'fuzzy_ahp' ? 'var(--color-purple-dark-1)' : 'var(--color-gold-dark-1)'
                  }}>
                    {project.evaluation_mode === 'fuzzy_ahp' ? '  AHP' : 
                     project.evaluation_mode === 'direct_input' ? ' ' :
                     project.evaluation_mode === 'theoretical' ? ' ' :
                     ' ()'}
                  </span>
                </div>
              </div>

              {/*   */}
              <div className="grid grid-cols-2 gap-3 mb-4">
                <div className="text-center p-2 rounded" style={{ backgroundColor: 'var(--bg-subtle)' }}>
                  <div className="text-xl font-bold" style={{ color: 'var(--text-primary)' }}>
                    {project.criteria_count || 0}
                  </div>
                  <div className="text-xs" style={{ color: 'var(--text-muted)' }}></div>
                </div>
                <div className="text-center p-2 rounded" style={{ backgroundColor: 'var(--bg-subtle)' }}>
                  <div className="text-xl font-bold" style={{ color: 'var(--text-primary)' }}>
                    {project.alternatives_count || 0}
                  </div>
                  <div className="text-xs" style={{ color: 'var(--text-muted)' }}></div>
                </div>
              </div>

              {/*   */}
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span style={{ color: 'var(--text-muted)' }}></span>
                  <span style={{ color: 'var(--text-primary)' }}>
                    {project.completionRate || 0}%
                  </span>
                </div>
                <div className="w-full rounded-full h-2" style={{ backgroundColor: 'var(--border-light)' }}>
                  <div 
                    className="h-2 rounded-full transition-all"
                    style={{ 
                      width: `${project.completionRate || 0}%`,
                      backgroundColor: getProgressColor(project.completionRate || 0)
                    }}
                  />
                </div>
                {/*    */}
                <div className="text-xs" style={{ color: 'var(--text-muted)' }}>
                  {getProgressText(project)}
                </div>
              </div>

              {/*   */}
              <div className="mt-4 pt-4 flex justify-between text-xs" style={{ borderTop: '1px solid var(--border-light)', color: 'var(--text-muted)' }}>
                <span>: {new Date(project.created_at || Date.now()).toLocaleDateString('ko-KR')}</span>
                {filter === 'trash' && project.deleted_at ? (
                  <span style={{ color: 'var(--text-danger)' }}>: {new Date(project.deleted_at).toLocaleDateString('ko-KR')}</span>
                ) : (
                  <span>: {new Date(project.updated_at || Date.now()).toLocaleDateString('ko-KR')}</span>
                )}
              </div>

              {/*   */}
              <div className="mt-4 pt-4 flex justify-end space-x-2" style={{ borderTop: '1px solid var(--border-light)' }}>
                {filter === 'trash' ? (
                  //  
                  <>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleRestoreProject(project.id || '', project.title);
                      }}
                      className="p-2 text-gray-400 hover:text-green-600 hover:bg-green-50 rounded-lg transition-colors"
                      title=""
                      type="button"
                    >
                      
                    </button>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handlePermanentDelete(project.id || '', project.title);
                      }}
                      className="p-2 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                      title=""
                      type="button"
                    >
                      
                    </button>
                  </>
                ) : (
                  //  
                  <>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleEditProject(project);
                      }}
                      className="p-2 text-gray-400 hover:text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                      title=""
                      type="button"
                    >
                      
                    </button>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleModelBuilder(project);
                      }}
                      className="p-2 text-gray-400 hover:text-green-600 hover:bg-green-50 rounded-lg transition-colors"
                      title=" "
                      type="button"
                    >
                      
                    </button>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleAnalysis(project);
                      }}
                      className="p-2 rounded-lg transition-colors"
                      style={{ color: 'var(--text-muted)' }}
                      onMouseEnter={(e) => {
                        e.currentTarget.style.color = 'var(--accent-primary)';
                        e.currentTarget.style.backgroundColor = 'var(--bg-subtle)';
                      }}
                      onMouseLeave={(e) => {
                        e.currentTarget.style.color = 'var(--text-muted)';
                        e.currentTarget.style.backgroundColor = 'transparent';
                      }}
                      title=" "
                      type="button"
                    >
                      
                    </button>
                    <button
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleDeleteWithConfirm(project);
                      }}
                      className="p-2 text-gray-400 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                      title=""
                      type="button"
                    >
                      
                    </button>
                  </>
                )}
              </div>
            </div>
          ))}
          </div>
        </div>
      )}

      {/*   */}
      <Modal
        isOpen={pendingAction !== null}
        onClose={() => { setPendingAction(null); setPermanentDeleteStep(1); }}
        title={
          pendingAction?.type === 'restore' ? ' ' :
          pendingAction?.type === 'permanentDelete' ? '  ' :
          ' '
        }
        size="sm"
        footer={
          <div className="flex justify-end space-x-2">
            <button
              onClick={() => { setPendingAction(null); setPermanentDeleteStep(1); }}
              className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
            >
              
            </button>
            <button
              onClick={
                pendingAction?.type === 'restore' ? confirmRestoreProject :
                pendingAction?.type === 'permanentDelete' ? confirmPermanentDelete :
                confirmMoveToTrash
              }
              className={`px-4 py-2 text-sm font-medium text-white rounded-md ${
                pendingAction?.type === 'permanentDelete'
                  ? 'bg-red-600 hover:bg-red-700'
                  : pendingAction?.type === 'restore'
                  ? 'bg-green-600 hover:bg-green-700'
                  : 'bg-orange-500 hover:bg-orange-600'
              }`}
            >
              {pendingAction?.type === 'restore' ? '' :
               pendingAction?.type === 'permanentDelete' && permanentDeleteStep === 1 ? '' :
               pendingAction?.type === 'permanentDelete' ? ' ' :
               ''}
            </button>
          </div>
        }
      >
        {pendingAction?.type === 'restore' && (
          <p className="text-sm text-gray-700">
            <strong>"{pendingAction.title}"</strong>  ?
          </p>
        )}
        {pendingAction?.type === 'permanentDelete' && permanentDeleteStep === 1 && (
          <div className="space-y-2">
            <p className="text-sm text-gray-700">
              <strong>"{pendingAction.title}"</strong>   ?
            </p>
            <p className="text-sm text-red-600 font-medium">     !</p>
          </div>
        )}
        {pendingAction?.type === 'permanentDelete' && permanentDeleteStep === 2 && (
          <div className="space-y-2">
            <p className="text-sm text-gray-700">
               <strong>"{pendingAction.title}"</strong>  ?
            </p>
            <p className="text-sm text-red-600 font-medium"> .</p>
          </div>
        )}
        {pendingAction?.type === 'trash' && (
          <div className="space-y-2">
            <p className="text-sm text-gray-700">
              <strong>"{pendingAction.title}"</strong>  ?
            </p>
            <p className="text-sm text-gray-500">     .</p>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default MyProjects;