import React, { useState, useEffect } from 'react';
import Card from '../common/Card';
import Button from '../common/Button';
import api from '../../services/api';

interface UsageManagementProps {
  user: {
    id: string | number;  //  number 
    first_name: string;
    last_name: string;
    email: string;
    role: string;
  };
  onBack?: () => void;
}

interface SubscriptionInfo {
  plan_type: string;
  start_date: string;
  end_date: string;
  status: string;
  days_remaining: number;
  trial_days: number;
  is_active: boolean;
}

interface UsageInfo {
  projects: number;
  evaluators: number;
  storage_mb: number;
  quota: {
    max_projects: number;
    max_evaluators_per_project: number;
    max_storage_mb: number;
    can_export: boolean;
    can_use_advanced_features: boolean;
  };
}

const UsageManagement: React.FC<UsageManagementProps> = ({ user, onBack }) => {
  const [subscription, setSubscription] = useState<SubscriptionInfo | null>(null);
  const [usage, setUsage] = useState<UsageInfo | null>(null);
  const [loading, setLoading] = useState(true);
  const [resetConfirm, setResetConfirm] = useState('');
  const [keepArchives, setKeepArchives] = useState(true);
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);
  const [showResetModal, setShowResetModal] = useState(false);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  //     
  useEffect(() => {
    loadSubscriptionData();
    loadUsageData();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadSubscriptionData = async () => {
    const response = await api.get('/api/subscription/status');
    if (response.success && response.data) {
      setSubscription(response.data.subscription);
    }
  };

  const loadUsageData = async () => {
    try {
      const response = await api.get('/api/subscription/usage');
      if (response.success && response.data) {
        setUsage(response.data.usage);
      } else if (!response.success) {
        showActionMessage('error', response.error || '   .');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleExtendTrial = async (days: number) => {
    const response = await api.post('/api/subscription/extend', {
      days,
      reason: 'manual_extension'
    });

    if (response.success) {
      showActionMessage('success', ` ${days} .`);
      loadSubscriptionData();
    } else {
      showActionMessage('error', ` : ${response.error || '   '}`);
    }
  };

  const handleResetData = async () => {
    if (!resetConfirm) {
      showActionMessage('error', ' .');
      return;
    }

    setShowResetModal(true);
  };

  const confirmResetData = async () => {
    setShowResetModal(false);

    const response = await api.post('/api/subscription/reset-data', {
      confirmPassword: resetConfirm,
      keepArchives
    });

    if (response.success) {
      showActionMessage('success', `  : ${response.data?.deleted_projects}  `);
      setResetConfirm('');
      loadUsageData();
    } else {
      showActionMessage('error', ` : ${response.error || '   '}`);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg" style={{ color: 'var(--text-muted)' }}>
             ...
        </div>
      </div>
    );
  }

  const getProgressColor = (current: number, max: number) => {
    const percentage = max > 0 ? (current / max) * 100 : 0;
    if (percentage >= 90) return '#ef4444'; // red
    if (percentage >= 70) return '#f59e0b'; // amber
    return 'var(--accent-primary)'; // primary
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="min-h-screen" style={{ backgroundColor: 'var(--bg-base)' }}>
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}

      {/*     */}
      {showResetModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black bg-opacity-50" onClick={() => setShowResetModal(false)} />
          <div className="relative bg-white rounded-xl shadow-2xl p-6 max-w-sm w-full mx-4">
            <h3 className="text-lg font-bold text-gray-900 mb-2">  </h3>
            <p className="text-gray-600 text-sm mb-6">
                 . ?
            </p>
            <div className="flex space-x-3">
              <button
                onClick={() => setShowResetModal(false)}
                className="flex-1 px-4 py-2 rounded-lg border border-gray-300 text-gray-700 text-sm font-medium hover:bg-gray-50 transition-colors"
              >
                
              </button>
              <button
                onClick={confirmResetData}
                className="flex-1 px-4 py-2 rounded-lg bg-red-600 text-white text-sm font-medium hover:bg-red-700 transition-colors"
              >
                 
              </button>
            </div>
          </div>
        </div>
      )}

      <div className="border-b sticky top-0 z-10" style={{
        backgroundColor: 'var(--bg-primary)',
        borderBottomColor: 'var(--border-subtle)'
      }}>
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="py-6">
            <div className="flex items-center justify-between">
              <div className="flex items-center">
                {onBack && (
                  <button
                    onClick={onBack}
                    className="mr-4 text-gray-500 hover:text-gray-700 transition-colors text-2xl"
                  >
                    
                  </button>
                )}
                <div>
                  <h1 className="text-3xl font-bold text-gray-900 flex items-center">
                    <span className="text-4xl mr-3"></span>
                     
                  </h1>
                  <p className="text-gray-600 mt-2"> ,     </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">

          {/*   */}
          <Card title=" ">
            {subscription ? (
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <span style={{ color: 'var(--text-secondary)' }}></span>
                  <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                    subscription.plan_type === 'premium' ? 'bg-blue-100 text-blue-800' :
                    subscription.plan_type === 'trial' ? 'bg-yellow-100 text-yellow-800' :
                    'bg-gray-100 text-gray-800'
                  }`}>
                    {subscription.plan_type === 'trial' ? '' :
                     subscription.plan_type === 'basic' ? '' :
                     subscription.plan_type === 'premium' ? '' : ''}
                  </span>
                </div>

                <div className="flex items-center justify-between">
                  <span style={{ color: 'var(--text-secondary)' }}></span>
                  <span>{formatDate(subscription.start_date)}</span>
                </div>

                <div className="flex items-center justify-between">
                  <span style={{ color: 'var(--text-secondary)' }}></span>
                  <span className={subscription.status === 'expired' ? 'text-red-600' : ''}>
                    {formatDate(subscription.end_date)}
                  </span>
                </div>

                <div className="flex items-center justify-between">
                  <span style={{ color: 'var(--text-secondary)' }}> </span>
                  <span className={`font-medium ${
                    subscription.days_remaining < 7 ? 'text-red-600' :
                    subscription.days_remaining < 30 ? 'text-yellow-600' :
                    'text-green-600'
                  }`}>
                    {subscription.days_remaining > 0 ? `${Math.ceil(subscription.days_remaining)}` : ''}
                  </span>
                </div>

                <div className="flex items-center justify-between">
                  <span style={{ color: 'var(--text-secondary)' }}></span>
                  <span className={`px-2 py-1 rounded text-sm ${
                    subscription.status === 'active' ? 'bg-green-100 text-green-800' :
                    'bg-red-100 text-red-800'
                  }`}>
                    {subscription.status === 'active' ? '' : ''}
                  </span>
                </div>

                {subscription.plan_type === 'trial' && subscription.days_remaining > 0 && (
                  <div className="pt-4 space-y-2">
                    <h4 className="font-medium" style={{ color: 'var(--text-primary)' }}>
                        
                    </h4>
                    <div className="flex space-x-2">
                      <Button
                        variant="secondary"
                        size="sm"
                        onClick={() => handleExtendTrial(7)}
                      >
                        +7
                      </Button>
                      <Button
                        variant="secondary"
                        size="sm"
                        onClick={() => handleExtendTrial(30)}
                      >
                        +30
                      </Button>
                    </div>
                  </div>
                )}
              </div>
            ) : (
              <div className="text-center py-8" style={{ color: 'var(--text-muted)' }}>
                    .
              </div>
            )}
          </Card>

          {/*   */}
          <Card title=" ">
            {usage ? (
              <div className="space-y-6">
                {/*   */}
                <div>
                  <div className="flex items-center justify-between mb-2">
                    <span style={{ color: 'var(--text-secondary)' }}></span>
                    <span className="text-sm font-medium">
                      {usage.projects}/{usage.quota.max_projects > 0 ? usage.quota.max_projects : ''}
                    </span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className="h-2 rounded-full transition-all duration-300"
                      style={{
                        width: `${Math.min((usage.projects / (usage.quota.max_projects || usage.projects || 1)) * 100, 100)}%`,
                        backgroundColor: getProgressColor(usage.projects, usage.quota.max_projects)
                      }}
                    ></div>
                  </div>
                </div>

                {/*   */}
                <div>
                  <div className="flex items-center justify-between mb-2">
                    <span style={{ color: 'var(--text-secondary)' }}>  </span>
                    <span className="text-sm font-medium">
                      {usage.evaluators}/{usage.quota.max_evaluators_per_project > 0 ? usage.quota.max_evaluators_per_project : ''} ()
                    </span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className="h-2 rounded-full transition-all duration-300"
                      style={{
                        width: `${Math.min((usage.evaluators / (usage.quota.max_evaluators_per_project || usage.evaluators || 1)) * 100, 100)}%`,
                        backgroundColor: getProgressColor(usage.evaluators, usage.quota.max_evaluators_per_project)
                      }}
                    ></div>
                  </div>
                </div>

                {/*  */}
                <div>
                  <div className="flex items-center justify-between mb-2">
                    <span style={{ color: 'var(--text-secondary)' }}></span>
                    <span className="text-sm font-medium">
                      {usage.storage_mb.toFixed(1)}MB/{usage.quota.max_storage_mb > 0 ? usage.quota.max_storage_mb : ''}MB
                    </span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className="h-2 rounded-full transition-all duration-300"
                      style={{
                        width: `${Math.min((usage.storage_mb / (usage.quota.max_storage_mb || usage.storage_mb || 1)) * 100, 100)}%`,
                        backgroundColor: getProgressColor(usage.storage_mb, usage.quota.max_storage_mb)
                      }}
                    ></div>
                  </div>
                </div>

                {/*   */}
                <div className="pt-4 border-t" style={{ borderColor: 'var(--border-subtle)' }}>
                  <h4 className="font-medium mb-3" style={{ color: 'var(--text-primary)' }}>
                      
                  </h4>
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-sm"> </span>
                      <span className={`text-sm ${usage.quota.can_export ? 'text-green-600' : 'text-red-600'}`}>
                        {usage.quota.can_export ? '  ' : ' '}
                      </span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span className="text-sm">  </span>
                      <span className={`text-sm ${usage.quota.can_use_advanced_features ? 'text-green-600' : 'text-red-600'}`}>
                        {usage.quota.can_use_advanced_features ? '  ' : ' '}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              <div className="text-center py-8" style={{ color: 'var(--text-muted)' }}>
                    .
              </div>
            )}
          </Card>

          {/*   */}
          <Card title=" ">
            <div className="overflow-x-auto">
              <table className="w-full text-sm">
                <thead>
                  <tr className="border-b" style={{ borderColor: 'var(--border-subtle)' }}>
                    <th className="text-left py-2" style={{ color: 'var(--text-secondary)' }}></th>
                    <th className="text-center py-2" style={{ color: 'var(--text-secondary)' }}></th>
                    <th className="text-center py-2" style={{ color: 'var(--text-secondary)' }}></th>
                    <th className="text-center py-2" style={{ color: 'var(--text-secondary)' }}></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="py-2"> </td>
                    <td className="text-center">3</td>
                    <td className="text-center">10</td>
                    <td className="text-center">50</td>
                  </tr>
                  <tr>
                    <td className="py-2">  ()</td>
                    <td className="text-center">5</td>
                    <td className="text-center">15</td>
                    <td className="text-center">100</td>
                  </tr>
                  <tr>
                    <td className="py-2"></td>
                    <td className="text-center">100MB</td>
                    <td className="text-center">500MB</td>
                    <td className="text-center">2GB</td>
                  </tr>
                  <tr>
                    <td className="py-2"></td>
                    <td className="text-center text-red-600"></td>
                    <td className="text-center text-green-600"></td>
                    <td className="text-center text-green-600"></td>
                  </tr>
                  <tr>
                    <td className="py-2"> </td>
                    <td className="text-center text-red-600"></td>
                    <td className="text-center text-red-600"></td>
                    <td className="text-center text-green-600"></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </Card>

          {/*   */}
          <Card title=" ">
            <div className="space-y-4">
              <div className="p-4 border-l-4 border-yellow-500 bg-yellow-50">
                <h4 className="font-medium text-yellow-800 mb-2"> </h4>
                <ul className="text-sm text-yellow-700 space-y-1">
                  <li>     </li>
                  <li>     </li>
                  <li>      </li>
                </ul>
              </div>

              <div>
                <label className="flex items-center space-x-2 mb-4">
                  <input
                    type="checkbox"
                    checked={keepArchives}
                    onChange={(e) => setKeepArchives(e.target.checked)}
                    className="rounded"
                  />
                  <span className="text-sm">    (2)</span>
                </label>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2" style={{ color: 'var(--text-secondary)' }}>
                   
                </label>
                <input
                  type="password"
                  value={resetConfirm}
                  onChange={(e) => setResetConfirm(e.target.value)}
                  className="w-full px-4 py-2 rounded-lg border transition-colors"
                  style={{
                    borderColor: 'var(--border-subtle)',
                    backgroundColor: 'var(--bg-elevated)'
                  }}
                  placeholder="  "
                />
              </div>

              <Button
                variant="primary"
                onClick={handleResetData}
                disabled={!resetConfirm}
                style={{
                  backgroundColor: '#ef4444',
                  borderColor: '#dc2626'
                }}
              >
                <span className="mr-2"></span>
                  
              </Button>
            </div>
          </Card>

          {/*   */}
          <Card title=" ">
            <div className="space-y-3">
              <div className="flex items-center justify-between">
                <span style={{ color: 'var(--text-secondary)' }}>  </span>
                <span className="font-medium">-</span>
              </div>
              <div className="flex items-center justify-between">
                <span style={{ color: 'var(--text-secondary)' }}> </span>
                <span className="font-medium">-</span>
              </div>
              <div className="flex items-center justify-between">
                <span style={{ color: 'var(--text-secondary)' }}> </span>
                <span className="font-medium">{usage?.projects || 0}</span>
              </div>
              <div className="flex items-center justify-between">
                <span style={{ color: 'var(--text-secondary)' }}> </span>
                <span className="font-medium">-</span>
              </div>
            </div>

            <div className="pt-4 mt-4 border-t" style={{ borderColor: 'var(--border-subtle)' }}>
              <p className="text-xs" style={{ color: 'var(--text-muted)' }}>
                *      .
              </p>
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default UsageManagement;
